import { Injectable } from '@angular/core';

//
@Injectable()
export class LightUserConfig {

    //
    public _loginFlag: boolean = false;

    //
    private _loginName: string = "";

    //
    private _loginPassword: string = "";

    //
    constructor(

    ) {

    }

    /**
     * 
     * @returns {string} 
     * @memberof LightUser
     */
    getLoginName(): string {
        return this._loginName;
    }

    //
    equalLoginName(loginName: string): boolean {
        var result: boolean = false;
        if (!!loginName && !!this._loginName) {
            if (this._loginName == loginName) {
                result = true;
            }
        }
        return result;
    }

    /**
     * 
     * @param {string} password 
     * @returns {boolean} 
     * 
     * @memberof LightUser
     */
    equalLoginPassword(password: string): boolean {
        var result: boolean = false;
        if (!!password && !!this._loginPassword) {
            if (this._loginPassword == password) {
                result = true;
            }
        }
        return result;
    }

    /**
     * 
     * @param {string}  
     * @param {string}  
     * 
     * @memberof LightUser
     */
    login(userName: string, loginPassword: string): void {
        this._loginName = userName;
        this._loginPassword = loginPassword;
        this._loginFlag = true;
    }

    //
    clear(): void {
        this._loginFlag = false;
        this._loginName = "";
        this._loginPassword = "";
    }

}



