/**
 * 
 * 
 * @export
 * @class LightTimeUtil
 */
export class LightTimeUtil {

    /**
     * 
     * 
     * @static
     * @param {Date}  
     * @param {string}  
     * @returns {string} 
     * 
     * @memberof LightTimeUtil
     */
    public static getTimeFormat(date: Date, format: string): string {
        var result: string = format;
        if (!!!result) {
            result = "yyyy-MM-dd hh:mm:ss";
        }
        var o = {
            "M+": date.getMonth() + 1, //
            "d+": date.getDate(), //
            "h+": date.getHours(), //
            "m+": date.getMinutes(), //
            "s+": date.getSeconds(), //
            "q+": Math.floor((date.getMonth() + 3) / 3), //
            "S": date.getMilliseconds() //
        };
        if (/(y+)/.test(result))
            result = result.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
        for (var k in o)
            if (new RegExp("(" + k + ")").test(result))
                result = result.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
        return result;
    }

    /**
     * 
     * @static
     * @param {number} 
     * @param {number}  
     * @param {number}  
     * @returns {Date} 
     * 
     * @memberof LightTimeUtil
     */
    public static createDateTime(year: number, mouth: number, day: number): Date {
        return new Date(year, mouth, day);
    }

}


