import { Injectable } from "@angular/core";
import { Keyboard } from "@ionic-native/keyboard";
import { LightNation } from "../common/lightNation";
import { Platform } from "ionic-angular";

/**
 * npm:npm install --save @ionic-native/keyboard
 * ionic:ionic cordova plugin add cordova-plugin-ionic-keyboard
 * :Android | iOS
 * 
 * @param {Keyboard} keyboard
 * @memberof lightKeyboard
 */
@Injectable()
export class LightKeyboard extends LightNation {
  constructor(public keyboard: Keyboard, public platform: Platform) {
    super();
  }

  // window.addEventListener('keyboardDidHide', () => {
  //     // Describe your logic which will be run each time keyboard is closed.
  // });
  // window.addEventListener('keyboardDidShow', (event) => {
  //     // Describe your logic which will be run each time when keyboard is about to be shown.
  //     console.log(event.keyboardHeight);
  // });
  // window.addEventListener('keyboardWillShow', (event) => {
  //     // Describe your logic which will be run each time when keyboard is about to be shown.
  //     console.log(event.keyboardHeight);
  // });
  // window.addEventListener('keyboardWillHide', () => {
  //     // Describe your logic which will be run each time when keyboard is about to be closed.
  // });

  public initKeyboard(window: any) {
    // if (window.cordova && window.Keyboard) {
    if (this.platform.is("cordova")) {
      this.keyboard.hideFormAccessoryBar(false);
    }
  }

  // onKeyboardShow()
  // IOS  ANDROID
  // observable
  //  Observable<any>
  public onKeyboardShow() {
    if (this.platform.is("cordova")) {
      return this.keyboard.onKeyboardShow();
    }
  }

  // onKeyboardHide()
  // IOS  ANDROID
  // observable
  //  Observable<any>
  public onKeyboardHide() {
    if (this.platform.is("cordova")) {
      return this.keyboard.onKeyboardHide();
    }
  }

  // onKeyboardWillShow()
  // IOS  ANDROID
  // observable
  //  Observable<any>
  public onKeyboardWillShow() {
    if (this.platform.is("cordova")) {
      return this.keyboard.onKeyboardWillShow();
    }
  }

  // onKeyboardWillHide()
  // IOS  ANDROID
  // observable
  //  Observable<any>
  public onKeyboardWillHide() {
    if (this.platform.is("cordova")) {
      return this.keyboard.onKeyboardWillHide();
    }
  }

  // setResizeMode(mode)
  // IOS
  // 
  // 	string
  public setResizeMode(mode: string) {
    if (this.platform.is("ios")) {
      return this.keyboard.setResizeMode(mode);
    }
  }

  // hideFormAccessoryBar(hide)
  // 
  // Param	Type	Details
  // hide	boolean
  public hideFormAccessoryBar(hide: boolean) {
    if (this.platform.is("cordova")) {
      return this.keyboard.hideFormAccessoryBar(hide);
    }
  }

  // show()
  // ANDROID
  // 
  public show() {
    if (!this.platform.is("ios")) {
      return this.keyboard.show();
    }
  }

  // hide()
  // IOS  ANDROID
  // 
  public hide() {
    if (this.platform.is("cordova")) {
      return this.keyboard.hide();
    }
  }
}



