import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { LightJsonUtil } from '../../light-util/light-util.module';
import { LightHttp } from '../../light-providers/light-providers.module';

@Component({
    // moduleId: module.id,
    selector: 'light-member-detail',
    templateUrl: 'light-member-detail.component.html',
    //styleUrls: ['./light-contact.component.scss']
})
export class LightMemberDetailComponent {

    //
    userName: string = "";

    //
    organzieName: string = "";

    //
    title: string = "";

    //
    mobilePhone: string = "";

    //
    mobilePhoneEx: string = "";

    //
    emailAddress: string = "";

    //
    fixedPhone: string = "";

    //
    mobileData: any = {};

    iphoneFlag: boolean = true;

    constructor(
        public navCtrl: NavController,
        public navParams: NavParams,
        public lightHttp: LightHttp
    ) {
        this.userName = navParams.get("userName");
        this.organzieName = navParams.get("organzieName");
        this.title = navParams.get("title");
    }

    ///
    ngOnInit(): void {
        this.lightHttp.post("User/GetUserInfo", { userAcount: this.userName }).then(
            value => {
                var itemObject: object = LightJsonUtil.toObject(value);
                this.mobilePhone = itemObject['Mobile'];
                this.mobilePhoneEx = this._getPhoneNumber(this.mobilePhone);
                this.fixedPhone = itemObject['OffcieTel'];
                this.emailAddress = itemObject['Email'];
                this.belonging(this.mobilePhone);
            }
        );
    }

    private _getPhoneNumber(mobileNumber: string): string {
        var result: string = "";
        if (!!mobileNumber) {
            result += mobileNumber.substr(0, 3);
            result += " ";
            result += mobileNumber.substr(3, 4);
            result += " ";
            result += mobileNumber.substr(7, 4);
            result += " ";
        }
        return result;
    }

    //
    private belonging(mobileNumber: string) {
        if (!!mobileNumber) {
            this.lightHttp.get("Belonging/Search", { phoneNumber: mobileNumber })
                .then((data) => {
                    if (LightJsonUtil.isJsonText(data)) {
                        this.mobileData = LightJsonUtil.toObject(data);
                    }
                })
            this.iphoneFlag = true;
        } else {
            this.iphoneFlag = false;
        }

    }

    callPhone() {
        if (!!this.mobilePhone) {
            window.location.href = "tel:" + this.mobilePhone;
            console.log("phone")
        }
    }

    callSms() {
        if (!!this.mobilePhone) {
            window.location.href = "sms:" + this.mobilePhone;
            console.log("callSms")
        }
    }

}



