import { Component } from '@angular/core';
import { LightProviders } from '../../../light-providers/light-providers.module';
import { NavController, ViewController, NavParams } from 'ionic-angular';

@Component({
    selector: 'handle-step',
    templateUrl: 'handle-step.component.html'
})
export class HandleStepComponent {

    /**
     * ID
     * @type {string}
     * @memberof HandleStepComponent
     */
    selectStepID: string = null;

    /**
     * 
     */
    selectList: Array<any> = null;

    /**
     * 
     * @param {LightProviders} lightProviders 
     * @param {ViewController} viewCtrl 
     * @param {NavController} navCtrl 
     * @param {NavParams} navParams 
     * @memberof HandleStepComponent
     */
    constructor(
        public lightProviders: LightProviders,
        public viewCtrl: ViewController,
        public navCtrl: NavController,
        public navParams: NavParams
    ) {
        this.selectList = navParams.data.data as Array<any>;
        //console.log("",dataList);
    }

    /**
    * 
    * @memberof HandleMutiContactComponent
    */
    goBack(): void {
        this.lightProviders.lightModal.dismiss(this.viewCtrl);
    }

    /**
     * 
     * @memberof HandleSinglePeopleComponent
     */
    userSelect() {
        //var userList=[];
        //userList.push(this.selectStepID);
        this.lightProviders.lightModal.dismiss(this.viewCtrl, this.selectStepID);
    }

}



