import { IFormList } from './IFormList';
import { FormActionMap } from './FormActionMap';
import { FormEvent } from './FormEvent';
import { FormRowBase } from './FormRowBase';
import { FormRowBaseUtil } from './FormRowBaseUtil';
import { FormList } from './FormList';
import { FormNode } from './FormNode';

/**
 * 
 * 
 * @export
 * @class FormRowUtil
 */
export class FormRowUtil {

    /**
     * 
     * @static
     * @param {Array<any>} dataList 
     * @param {FormEvent} formEvent 
     * @returns {Array<IFormList>} 
     * @memberof FormRowUtil
     */
    public static getFormDta(dataList: Array<any>, formEvent: FormEvent = null): Array<IFormList> {
        var result: Array<IFormList> = [];
        var tempRowList: IFormList = null;
        var list: Array<IFormList> = [];
        if (!!dataList && dataList.length > 0) {
            for (var index = 0; index < dataList.length; index++) {
                var element = dataList[index];
                if (element != null) {
                    var rowBaseType: FormRowBase = FormRowUtil._getRowBase(element);
                    if (FormRowBaseUtil.isStartNode(rowBaseType.type)) {
                        if (rowBaseType.type == "groupbegin") {
                            tempRowList = new FormList(element, formEvent);
                            result.push(tempRowList);
                        }
                        else {
                            list.push(tempRowList);
                            var next: FormList = new FormList(element, formEvent);
                            tempRowList.addRowData(next);
                            tempRowList = next;
                        }
                    } else if (FormRowBaseUtil.isEndNode(rowBaseType.type)) {
                        tempRowList.endRow = new FormNode(element, formEvent);
                        tempRowList = list.pop();
                    } else {
                        var node: any = new FormNode(element, formEvent);
                        tempRowList.addRowData(node);
                    }
                }
            }
        }
        return result;
    }

    /**
     * 
     * 
     * @private
     * @static
     * @param {*} element 
     * @returns {FormRowBase} 
     * @memberof FormRowUtil
     */
    private static _getRowBase(element: any): FormRowBase {
        return new FormRowBase(element);
    }

    /**
     * 
     * 
     * @static
     * @param {Array<IFormList>} dataList 
     * @memberof FormRowUtil
     */
    public static getActiveMapList(dataList: Array<IFormList>): FormActionMap {
        return new FormActionMap();
    }

}



