import { LoadingController, Loading } from 'ionic-angular';
import { Injectable } from '@angular/core';

@Injectable()
export class LightLoading {

    private autoTimeClose: number = 100000;//

    private loader: any = null; //

    constructor(
        public loadingCtrl: LoadingController
    ) {

    }

    /**
     * 
     * 
     * @param {*} func 
     * @memberof LightLoading
     */
    public presentLoading(title: string, completeHandler?: Function): Promise<any> {
        this.loader = this.loadingCtrl.create({
            content: title,
            duration: this.autoTimeClose,
        });
        if (!!completeHandler) {
            this.loader.onDidDismiss(() => {
                completeHandler();
            });
        }
        return this.loader.present();
    }

    /**
     * 
     * 
     * @param {any}
     * @memberof LightLoading
     */
    public stopLoading(): void {
        this.loader.dismiss();
    }

    /**
     * 
     * loader.dismiss()
     * @param {string}  
     * 
     * @memberof LightLoading
     */
    public showLoading(content: string = "..."): Loading {
        var loading = this.loadingCtrl.create({ spinner: 'circles', content: content, duration: this.autoTimeClose });
        loading.present();
        return loading;
    }

}


