import { Animation, PageTransition } from 'ionic-angular';

export class ModalFromBottomEnter extends PageTransition {
  init() {
    super.init();
    const ele: HTMLElement = this.enteringView.pageRef().nativeElement;
    const backdropEle = ele.querySelector('ion-backdrop');
    const backdrop = new Animation(this.plt, backdropEle);
    // 
    backdrop.beforeStyles({ 'z-index': 0, 'opacity': 0.3, 'visibility': 'visible' });

    const wrapper = new Animation(this.plt, ele.querySelector('.modal-wrapper'));
    const contentWrapper = new Animation(this.plt, ele.querySelector('ion-content.content'));
    // 
    contentWrapper.beforeStyles({ 'width': '100%', 'height': '100%' });

    // 
    wrapper.beforeStyles({ 'opacity': 1, 'width': '70%', 'height': '50%' });
    // translateYY0-1
    wrapper.fromTo('translateY', '-50%', '50%');
    wrapper.fromTo('translateX', '50%', '20%');

    // 
    backdrop.fromTo('opacity', 0.3, 0.3);

    this
      .element(this.enteringView.pageRef())
      // .easing('ease-in')
      .duration(200)
      .add(backdrop)
      .add(wrapper)
      .add(contentWrapper);

  }
}

export class ModalFromBottomLeave extends PageTransition {
  init() {
    super.init();
    const ele: HTMLElement = this.leavingView.pageRef().nativeElement;
    let backdrop = new Animation(this.plt, ele.querySelector('ion-backdrop'));
    let wrapperEle = <HTMLElement>ele.querySelector('.modal-wrapper');
    // let wrapperEleRect = wrapperEle.getBoundingClientRect();
    let wrapper = new Animation(this.plt, wrapperEle);


    backdrop.beforeStyles({ 'visibility': 'hidden' });

    // height of the screen - top of the container tells us how much to scoot it down
    // so it's off-screen
    wrapper.fromTo('translateY', '50%', '0%');
    wrapper.fromTo('translateX', '20%', '50%');

    backdrop.fromTo('opacity', 0.3, 0);

    this
      .element(this.leavingView.pageRef())
      .easing('ease-out')
      .duration(250)
      .add(backdrop)
      .add(wrapper);
  }
}


