import { Component, Input, OnInit } from '@angular/core';
import { IFormNode } from '../../light-common/index';

@Component({
    selector: 'light-read-div',
    templateUrl: 'light-read-div.component.html'
})
export class LightReadDivComponent implements OnInit {

    ///
    @Input() formRow: IFormNode = null;

    ///
    rowType: string = "";

    attachList: any;

    /**
     * 
     * 
     * @memberof LightReadDivComponent
     */
    public ngOnInit(): void {
        this.rowType = this.formRow.type;
        this.attachList = this.formRow.value;
    }
}



