import { Component, OnInit } from '@angular/core';
import { LightWriterCommon } from '../../common/index';

@Component({
    //moduleId: module.id,
    selector: 'writer-check',
    templateUrl: 'writer-check.component.html',
    //styleUrls: ['writer-check.component.scss']
})
export class WriterCheckComponent extends LightWriterCommon implements OnInit {

    //
    dataList: Array<any> = [];

    /**
     * 
     * 
     * @memberof WriterCheckComponent
     */
    public ngOnInit(): void {
        super.ngOnInit();
        this.dataList = this.getExtendObjectValue("enumeration");
    }

}



