import { Component } from '@angular/core';
import { LightProviders } from '../../../light-providers/light-providers.module';
import { NavController, ViewController, NavParams } from 'ionic-angular';

@Component({
    selector: 'handle-single-people',
    templateUrl: 'handle-single-people.component.html'
})
export class HandleSinglePeopleComponent {

    /**
     * 
     * @type {Array<any>}
     * @memberof HandleMutiPeopleComponent
     */
    userList: Array<any> = [];

    /**
     * 
     * @type {string}
     * @memberof HandleSinglePeopleComponent
     */
    selectUser: string = null;

    /**
     * 
     * @param {LightProviders} lightProviders 
     * @param {ViewController} viewCtrl 
     * @param {NavController} navCtrl 
     * @param {NavParams} navParams 
     * @memberof HandleSinglePeopleComponent
     */
    constructor(
        public lightProviders: LightProviders,
        public viewCtrl: ViewController,
        public navCtrl: NavController,
        public navParams: NavParams
    ) {
        var dataList = navParams.data.data as Array<any>;
        if (!!dataList && dataList.length > 0) {
            dataList.forEach(element => {
                this.userList.push({ UserName: element, Select: false });
            });
        }
    }

    /**
     * 
     * @memberof HandleMutiContactComponent
     */
    goBack(): void {
        this.lightProviders.lightModal.dismiss(this.viewCtrl);
    }

    /**
     * 
     * @memberof HandleSinglePeopleComponent
     */
    userSelect() {
        var userList = [];
        userList.push(this.selectUser);
        this.lightProviders.lightModal.dismiss(this.viewCtrl, userList);
    }
}



