import { Component, Input, OnInit, Output, EventEmitter } from '@angular/core';
import { IFormList, IFormRow, IFormNode, FormNode } from '../../light-common/index';
import { LightJsonUtil, LightArrayUtil } from '../../../light-util/light-util.module';
import { LightProviders } from '../../../light-providers/light-providers.module';

@Component({
    selector: 'light-writer-table',
    templateUrl: 'light-writer-table.component.html'
})
export class LightWriterTableComponent implements OnInit {

    //
    @Input() rowList: IFormList;

    //
    @Input() writerData: Array<any> = [];

    @Output() changeWriterData: EventEmitter<any> = new EventEmitter<any>();

    /**
     * 
     * 
     * @type {String}
     * @memberof LightWriterTableComponent
     */
    title: String = null;

    /**
     * 
     * @type {Array<any>}
     * @memberof LightWriterTableComponent
     */
    tableRowList: Array<any> = null;

    /**
     * 
     * 
     * @memberof LightWriterTableComponent
     */
    constructor(public lightProviders: LightProviders) {

    }

    //
    ngOnInit(): void {
        this.tableRowList = this._getRowList();
        this.title = this.rowList.title;
    }

    /**
     * 
     * 
     * @private
     * @returns {Array<any>} 
     * @memberof LightWriterTableComponent
     */
    private _getRowList(): Array<any> {
        var result: Array<any> = [];
        var rowInfoList: IFormRow[] = this.rowList.childData;
        var groupList: IFormRow[] = null;
        for (var i = 0; i < rowInfoList.length; i++) {
            var rowInfo: IFormRow = rowInfoList[i];
            if (rowInfo.type == "rowbegin") {
                groupList = [];
                result.push(groupList);
            }
            groupList.push(rowInfo);
        }
        return result;
    }

    /**
     * 
     * 
     * @private
     * @memberof LightWriterTableComponent
     */
    public deleteRow(objectList: IFormRow[]): void {
        if (objectList != null) {
            if (this.tableRowList.length > 1) {
                objectList.forEach(element => {
                    LightArrayUtil.remove(this.writerData, element.dataObject);
                });
                LightArrayUtil.remove(this.tableRowList, objectList);
                // 
                this.changeWriterData.emit();
            } else {
                this.lightProviders.lightPop.errMessage("");
            }
        }
    }

    /**
     * 
     * @memberof LightWriterTableComponent
     */
    public addRow(): void {
        var fristRow: IFormNode[] = this.tableRowList[0];
        var jsonText = LightJsonUtil.toJson(fristRow);
        var objectList = LightJsonUtil.toObject(jsonText) as IFormNode[];
        var dataList: Array<IFormNode> = [];
        objectList.forEach(element => {
            var formNode: FormNode = new FormNode(element.dataObject);
            formNode.value = "";
            dataList.push(formNode);
        });
        this._addDataLast(dataList);
        this.tableRowList.push(dataList);
        //         
        this.changeWriterData.emit();
    }

    /**
     * 
     * 
     * @private
     * @param {IFormRow[]} objectList 
     * @memberof LightWriterTableComponent
     */
    private _addDataLast(objectList: IFormRow[]) {
        var lastRow: IFormRow[] = this.tableRowList[this.tableRowList.length - 1];
        var lastRowInfo: IFormRow = lastRow[lastRow.length - 1];
        var index: number = LightArrayUtil.getIndexOf(this.writerData, lastRowInfo.dataObject);
        for (var i = 0; i < objectList.length; i++) {
            this.writerData.splice(index + 1 + i, 0, objectList[i].dataObject);
        }
    }

}



