import { LightWriterService } from './light-writer.service';
import { Component, OnInit, Input, SimpleChanges, OnDestroy } from '@angular/core';
import { FormRowUtil, IFormList, FormActionMap, FormEvent } from '../light-common/index';
import { LightWriterConfig } from "./light-writer.config";
import { Subscription } from 'rxjs/Subscription';

@Component({
    selector: 'light-writer',
    templateUrl: 'light-writer.component.html'
})
export class LightWriterComponent implements OnInit, OnDestroy {

    //
    @Input() writerData: any = {};

    //
    formListArray: Array<IFormList> = [];

    //
    formActionMap: FormActionMap = null;

    //
    formEvent: FormEvent = null;

    //
    formConfig: LightWriterConfig = new LightWriterConfig();

    //
    @Input() headFlag: boolean = true;

    subscription :Subscription;

    /**
     * 
     * @memberof LightWriterComponent
     */
    constructor(
        public lightWriterService: LightWriterService
    ) {

    }

    /**
     * 
     * @memberof LightReadComponent
     */
    public ngOnInit(): void {
        this.formListArray = [];
        this.formActionMap = new FormActionMap();
        this.formEvent = new FormEvent();
        this.formConfig.headFlag = this.headFlag;
        this._flush();

        if (!this.subscription && !!this.writerData) {
            console.log(this.subscription)
            this.lightWriterService.writerData = this.writerData;
            let that = this;
            this.subscription = this.lightWriterService.observable.subscribe({
                next(writerData) {
                    console.log(writerData)
                    that.lightWriterService.writerData = writerData;
                    that.writerData = writerData;
                    that._flush();
                }
            })
        }
    }

    ngOnDestroy(): void {
        if (this.subscription) {
            this.subscription.unsubscribe();
        }
    }

    //
    ngOnChanges(changes: SimpleChanges): void {
        this.ngOnInit();
    }

    /**
     * 
     * @private
     * @memberof LightReadComponent
     */
    private _flush(): void {
        if (!!this.writerData) {
            this.formListArray = FormRowUtil.getFormDta(this.writerData, this.formEvent);
        }
    }

    changeWriterDataEvent() {
        // 
        this._flush();
    }

}



