import { LightHttp } from '../../light-providers/light-providers.module';

/**
 * 
 * 
 * @export
 * @class LightWriterFlow
 */
export class LightWriterFlow {

    /**
     * ID
     * @type {string}
     * @memberof LightWriterFlow
     */
    public objectID: string = null;

    /**
     * 
     * @type {*}
     * @memberof LightWriterFlow
     */
    public formContent: any = null;

    /**
     * 
     * 
     * @type {string}
     * @memberof LightWriterFlow
     */
    public curStepObject: any = null;

    /**
     * 
     * @type {string}
     * @memberof LightWriterFlow
     */
    public nextStepObject: any = null;

    /**
     * 
     * 
     * @type {*}
     * @memberof LightWriterFlow
     */
    public nextStepUserObject: any = null;

    /**
     * 
     */
    public preStepObject: any = null;

    /**
     * ID
     * @type {string}
     * @memberof LightWriterFlow
     */
    public selectStepID: string = null;

    /**
     * 
     * @param {LightHttp} lightHttp 
     * @memberof LightWriterFlow
     */
    constructor(public lightHttp: LightHttp) {

    }

    /**
     * ID
     * 
     * @returns {string} 
     * @memberof LightWriterFlow
     */
    public getCurStepID(): string {
        var result: string = null;
        if (!!this.curStepObject) {
            result = this.curStepObject.ID;
        }
        return result;
    }

    /**
     * 
     * 
     * @returns {string} 
     * @memberof LightWriterFlow
     */
    public getCurStepTitle(): string {
        var result: string = null;
        if (!!this.curStepObject) {
            result = this.curStepObject.Title;
        }
        return result;
    }

    /**
     * ID
     * 
     * @returns {string} 
     * @memberof LightWriterFlow
     */
    public getNextStepList(): any {
        return this.nextStepObject;
    }

    /**
     * 
     * 
     * @returns {boolean} 
     * @memberof LightWriterFlow
     */
    public isNextStepSingle(): boolean {
        var result: boolean = false;
        if (!!this.nextStepObject && this.nextStepObject.length == 1) {
            result = true;
        }
        return result;
    }

    /**
     * ID
     * 
     * @returns {string} 
     * @memberof LightWriterFlow
     */
    public getNextStepFristID(): string {
        var result: string = "";
        if (!!this.nextStepObject && this.nextStepObject.length > 0) {
            result = this.nextStepObject[0].ID;
        }
        return result;
    }

    /**
     * ID
     * 
     * @returns {string} 
     * @memberof LightWriterFlow
     */
    public getSelectStepID(): string {
        var result: string = "";
        if (!!!this.selectStepID) {
            result = this.getNextStepFristID();
        } else {
            result = this.selectStepID;
        }
        return result;
    }

    /**
     * 
     * 
     * @returns {string} 
     * @memberof LightWriterFlow
     */
    public getNextStepUserType(): string {
        var result: string = null;
        if (!!this.nextStepUserObject) {
            result = this.nextStepUserObject.UserType;
        }
        return result;
    }

    /**
     * 
     * 
     * @returns {boolean} 
     * @memberof LightWriterFlow
     */
    public isNextStepUserSingle(): boolean {
        var result: boolean = false;
        var userType = this.getNextStepUserType();
        if (!!userType && userType == "single") {
            result = true;
        }
        return result;
    }

    /**
     * 
     * 
     * @returns {string} 
     * @memberof LightWriterFlow
     */
    public getNextStepUserList(): Array<any> {
        var result: Array<any> = null;
        if (!!this.nextStepUserObject) {
            result = this.nextStepUserObject.UserList as Array<any>;
        }
        return result;
    }

    /**
     * 
     * @returns {boolean} 
     * @memberof LightWriterFlow
     */
    public isArchiveFlow(): boolean {
        var result: boolean = true;
        if (!!this.getNextStepFristID()) {
            result = false;
        }
        return result;
    }

    /**
     * 
     */
    public isAutoSubmit(): boolean {
        var result: boolean = false;
        if (this.isNextStepUserSingle()) {
            var userList = this.getNextStepUserList();
            if (!!userList && userList.length == 1) {
                result = true;
            }
        }
        return result;
    }

    /**
     * 
     * 
     * @returns {boolean} 
     * @memberof LightWriterFlow
     */
    public isFristStep(): boolean {
        var result: boolean = false;
        if (!!!this.preStepObject) {
            result = true;
        }
        return result;
    }

}


