import { Directive, OnInit, OnDestroy, Input, Renderer2, AfterContentInit } from '@angular/core';
import { Content } from 'ionic-angular';

@Directive({
  selector: '[prevent-cover-input]' // Attribute selector
})
export class PreventCoverInputDirective implements OnInit, OnDestroy, AfterContentInit {

  /**
   *
   *
   * @memberof PreventCoverInputDirective
   */
  @Input('prevent-cover-input') content: Content;

  // 
  listenFunc: () => void;
  // 
  contentEle: any;

  /**
   * Creates an instance of PreventCoverInputDirective.
   * @param {Renderer2} renderer this
   * @memberof PreventCoverInputDirective
   */
  constructor(
    private renderer: Renderer2
  ) {
    console.log('PreventCoverInputDirective  Start...');
  }

  ngOnInit(): void {
    // 
    this.listenFunc = this.renderer.listen(
      'window',
      'keyboardDidShow',
      this.scrollEvent.bind(this)
    )
  }

  ngAfterContentInit(): void {
    // 
    this.contentEle = this.content._elementRef.nativeElement;
    // textarea
    this.createHideTextarea();
  }

  ngOnDestroy(): void {
    // 
    this.listenFunc()
  }

  /**
   *
   *
   * @param {*} e 
   * @memberof WorkNewProcessPage
   */
  scrollEvent(e: any) {
    // scroll
    let viewTop = this.contentEle.querySelector('ion-grid').getBoundingClientRect().top;

    // inputtextarea
    let focusTop = 0;
    let focusEle: any;
    if (focusEle = document.querySelector('input:focus')) {
      focusTop = focusEle.getBoundingClientRect().top;
    } else if (focusEle = document.querySelector('textarea:focus')) {
      let textareaLineheight = this.getStyle('textarea:focus', 'line-height')
      let textareaHide: HTMLTextAreaElement = document.querySelector('textarea#textareaHide');
      textareaHide.style.width = focusEle.clientWidth + 'px';
      textareaHide.style.lineHeight = textareaLineheight;
      textareaHide.value = focusEle.value.slice(0, focusEle.selectionStart);
      focusTop = focusEle.getBoundingClientRect().top + textareaHide.scrollHeight - parseInt(textareaLineheight);
    }

    // 
    let viewHeight = parseInt(this.getStyle('.scroll-content', 'height'));

    // 
    this.content.scrollTo(0, -viewTop + focusTop - viewHeight / 2)
  }

  /**
   *
   *
   * @private
   * @param {string} ele
   * @param {string} propertyName
   * @returns
   * @memberof PreventCoverInputDirective
   */
  private getStyle(ele: string, propertyName: string) {
    return window.getComputedStyle(this.contentEle.querySelector(ele)).getPropertyValue(propertyName);
  }

  /**
   *
   *
   * @memberof PreventCoverInputDirective
   */
  private createHideTextarea() {
    if (!document.querySelector('textarea#textareaHide')) {
      let textareaHide = document.createElement('textarea')
      textareaHide.style.zIndex = '-1';
      textareaHide.style.position = 'absolute';
      textareaHide.style.top = '0';
      textareaHide.style.outline = 'none';
      textareaHide.style.border = 'none';
      textareaHide.style.padding = '0';
      textareaHide.style.visibility = 'hidden';
      textareaHide.rows = 1;
      textareaHide.id = 'textareaHide';
      this.contentEle.appendChild(textareaHide);
    }
  }
}



