import { Component, Input, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';

@Component({
    selector: 'light-date',
    templateUrl: 'light-date.component.html'
})
export class LightDateComponent implements OnInit {

  @Input() dateArr: {
    'txt': string, // 
    'value': string, // 
    'type': string, // 
    'extend': {
      'fieldtype': string // 
    }
  }[];

  constructor() {}

  ngOnInit() {}

  /**
   *ionic
   *
   * @param {*} type
   * @memberof LightDateComponent
   */
  parseType(type) {
    switch (type) {
      case 'dateyymmdd':
        return 'YYYY/MM/DD';
      case 'dateyymm':
        return 'YYYY-MM-DD HH:mm';
      case 'datetime':
        return 'YYYY/MM';
      default:
        return false;
    }
  }

  /**
   *
   *
   * @param {string} fieldtype //
   * @param {NgForm} dateForm  //
   * @memberof LightDateComponent
   */
  dateChange(fieldtype: string, dateForm: NgForm) {
    switch (fieldtype) {
      case 'start':
        this.rule1(dateForm);
        break;
      case 'end':
        this.rule1(dateForm);
        break;
      default:
        break;
    }
  }

  /**
   * =  - 
   *
   * @param {NgForm} dateForm // 
   * @memberof LightDateComponent
   */
  rule1(dateForm: NgForm) {
    // 
    let data = dateForm.value as {
      start: string, // 
      end: string, // 
      interval: string // 
    };
    let {start, end} = data;
    if(start && end && 'interval' in data) {
      this.dateArr.map((item => {
        if(item.extend.fieldtype == 'interval') {
          item.value = ((new Date(end).getTime() - new Date(start).getTime()) / 86400000).toFixed(1) + '';
        }
      }))
    }
  }
}



