import { Component } from '@angular/core';
import { NavParams, ViewController } from 'ionic-angular';
import { LightProviders } from '../../../light-providers/light-providers.module';

@Component({
  selector: 'handle-muti-people',
  templateUrl: 'handle-muti-people.component.html'
})
export class HandleMutiPeopleComponent {

  /**
   * 
   * @type {Array<any>}
   * @memberof HandleMutiPeopleComponent
   */
  userList: Array<any> = [];

  /**
   * 
   * @param {LightProviders} lightProviders 
   * @param {ViewController} viewCtrl 
   * @memberof HandleMutiPeopleComponent
   */
  constructor(
    public lightProviders: LightProviders,
    public viewCtrl: ViewController,
    public navParams: NavParams
  ) {
    var dataList = navParams.data.data as Array<any>;
    if (!!dataList && dataList.length > 0) {
      dataList.forEach(element => {
        this.userList.push({ UserName: element, Select: false });
      });
    }
  }

  /**
   * 
   * @memberof HandleMutiContactComponent
   */
  goBack(): void {
    this.lightProviders.lightModal.dismiss(this.viewCtrl);
  }

  /**
   * 
   * @memberof HandleMutiContactComponent
   */
  submit(): void {
    var selectUser = this._getUserList();
    if (selectUser.length == 0) {
      this.lightProviders.lightPop.errMessage("");
    } else {
      this.lightProviders.lightModal.dismiss(this.viewCtrl, selectUser);
    }
  }

  /**
   * 
   * @private
   * @returns {Array<any>} 
   * @memberof HandleMutiPeopleComponent
   */
  private _getUserList(): Array<any> {
    var result = [];
    this.userList.forEach(element => {
      if (element.Select) {
        result.push(element.UserName);
      }
    });
    return result;
  }

}



