import { Injectable } from "@angular/core";
import { LightStringUtil } from "../../light-util/light-util.module";

/**
 * 
 *
 * cordova
 * @export
 * @abstract
 * @class LightNation
 */
@Injectable()
export abstract class LightNation {
  constructor() {}

  _isWeb(): boolean {
    var result = false;
    var ua = window.navigator.userAgent.toLowerCase();
    if (
      LightStringUtil.contain(ua, "windows") ||
      LightStringUtil.contain(ua, "macintosh")
    ) {
      result = true;
    }
    return result;
  }

  /**
   * 
   *
   * @param {Promise<any>} defaultResult
   * @returns {Promise<any>}
   * @memberof LightNation
   */
  _getDefaultResult(defaultResult: any): Promise<any> {
    var result: Promise<any> = new Promise(function(resolve, reject) {
      resolve(defaultResult);
    });
    return result;
  }
}



