import { LightArrayUtil } from "./lightArrayUtil";

/**
 * 
 * @export
 * @class MapList
 */
export class MapList {
  keyList: Array<any>;

  list: object;

  constructor() {
    this.keyList = new Array<any>();
    this.list = {};
  }

  /**
   * Key
   * @param {string} name
   * @returns {boolean}
   *
   * @memberof MapList
   */
  containkey(name: string): boolean {
    var result = false;
    if (!!name && this.list.hasOwnProperty(name.toUpperCase())) {
      result = true;
    }
    return result;
  }

  /**
   * 
   *
   * @param {string} key
   * @param {*} 
   *
   * @memberof MapList
   */
  put(name: string, value: any): void {
    if (!this.containkey(name)) {
      this.keyList.push(name.toUpperCase());
    }
    this.list[name.toUpperCase()] = value;
  }

  /**
   * 
   * @param {string} key
   * @returns {*}
   *
   * @memberof MapList
   */
  getValue(key: string): any {
    var result = null;
    if (!!key && this.containkey(key)) {
      result = this.list[key.toUpperCase()];
    }
    return result;
  }

  /**
   * 
   * @returns {number}
   * @memberof MapList
   */
  size(): number {
    return this.keyList.length;
  }

  /**
   * key
   * @returns
   * @memberof MapList
   */
  getkeylist() {
    return this.keyList;
  }

  /**
   * 
   * @param {MapList} 
   *
   * @memberof MapList
   */
  addMapList(data: MapList) {
    if (!!data) {
      var dataList = data.getkeylist();
      for (var i: number = 0; i < dataList.length; i++) {
        this.put(dataList[i], data.getValue(dataList[i]));
      }
    }
  }

  /**
   * 
   *
   * @param {string} 
   *
   * @memberof MapList
   */
  remove(name: string): void {
    if (!!name && this.containkey(name)) {
      delete this.list[name.toUpperCase()];
      LightArrayUtil.remove(this.keyList, name.toUpperCase());
    }
  }

  /**
   * 
   * @memberof MapList
   */
  clear(): void {
    this.keyList = new Array<any>();
    this.list = {};
  }
}



