import { Component, Input, EventEmitter, Output, OnInit } from '@angular/core';
import { IFormRow } from '../../light-common/index';
import { LightProviders } from '../../../light-providers/light-providers.module';

@Component({
    selector: 'light-writer-row',
    templateUrl: 'light-writer-row.component.html'
})
export class LightWriterRowComponent implements OnInit {

    //
    @Input() rowList: IFormRow[];

    /**
     * 
     * @type {EventEmitter<any>}
     * @memberof GeoComponent
     */
    @Output() deleteClick: EventEmitter<any> = new EventEmitter<any>();

    @Output() addClick: EventEmitter<any> = new EventEmitter<any>();

    //
    title: String = null;

    showFlag: boolean = true;

    /**
     * 
     */
    constructor(
        public lightProviders: LightProviders
    ) {

    }

    /**
     * 
     * 
     * @memberof LightWriterRowComponent
     */
    deleteEvent(): void {
        this.lightProviders.lightPop.confirm("").then(() => {
            this.deleteClick.emit(this.rowList);
        }).catch(() => {

        });
    }

    /**
     *
     *
     * @memberof LightWriterRowComponent
     */
    addEvent(): void {
        this.addClick.emit();
    }

    /**
     * 
     * 
     * @memberof LightWriterRowComponent
     */
    public ngOnInit(): void {
        if (this.rowList.length > 1) {
            this.title = this.rowList[0].title;
        }
    }

    showEvent(): void {
        if (this.showFlag) {
            this.showFlag = false;
        } else {
            this.showFlag = true;
        }
    }

}



