import { Injectable } from "@angular/core";
import { Diagnostic } from "@ionic-native/diagnostic";
import { Observable } from "rxjs/Observable";
import { Platform, AlertController } from "ionic-angular";
import { LatLng, WebSpatialReference } from "./LatlngUtil";

declare var baidumap_location: any; //

/**
 * npm: npm install --save @ionic-native/diagnostic;
 * ionic:ionic cordova plugin add cordova.plugins.diagnostic;
 * ionic:ionic cordova plugin add cordova-plugin-baidumaplocation@3.2.0 --variable ANDROID_KEY="dcbZsdvqalbBTDEE03GoZro3CwNgNGO0" --variable IOS_KEY="skm1oP6HDZW33atFknc0dSIf0MnFellH"
 * ionic:ionic cordova plugin add cordova-plugin-baidumaplocation --variable ANDROID_KEY="dcbZsdvqalbBTDEE03GoZro3CwNgNGO0" --variable IOS_KEY="skm1oP6HDZW33atFknc0dSIf0MnFellH"
 * :Android | iOS
 * 
 * @memberof LightLocationService
 */
@Injectable()
export class LightLocationService {
  private enabledLocationService = false; //

  private locationAuthorization = false; //

  constructor(
    private diagnostic: Diagnostic,
    private platform: Platform,
    private alertCtrl: AlertController
  ) {
    console.log("Hello LightLocationServiceProvider Provider");
  }

  /**
   * 
   *
   * @returns {boolean}
   * @memberof LightLocationService
   */
  public isMobile(): boolean {
    return this.platform.is("mobile") && !this.platform.is("mobileweb");
  }

  /**
   * 
   */
  public getUserLocation(): Observable<any> {
    return Observable.create(observer => {
      if (this.isMobile()) {
        this.assertLocationService().subscribe(
          res => {
            if (res) {
              this.assertLocationAuthorization().subscribe(
                res => {
                  if (res) {
                    return this.getLocation(observer);
                  }
                },
                err => {
                  observer.error(err);
                }
              );
            }
          },
          err => {
            observer.error(err);
          }
        );
      } else {
        console.log(",");
        observer.next(new LatLng(41.786438059549, 123.362592840223));
      }
    });
  }

  /**
   * 
   *
   * @private
   * @param {any} observer
   * @memberof LightLocationService
   */
  private getLocation(observer: any) {
    baidumap_location &&
      baidumap_location.getCurrentPosition(
        data => {
          observer.next(
            new LatLng(
              data.latitude,
              data.longitude,
              WebSpatialReference.BD_09
            ).toWGS_84()
          );
        },
        errMsg => {
          if (errMsg.code === errMsg.PERMISSION_DENIED) {
            this.openRequestPremission();
          } else if (errMsg.code === errMsg.POSITION_UNAVAILABLE) {
            alert(",WIFIWIFI");
          } else if (errMsg.code === errMsg.TIMEOUT) {
            alert(",");
          }
          observer.error("");
        }
      );
  }

  /**
   * 
   *
   * @private
   * @memberof LightLocationService
   */
  private openRequestPremission(): void {
    this.alertCtrl
      .create({
        title: "",
        subTitle: "app",
        buttons: [
          {
            text: ""
          },
          {
            text: "",
            handler: () => {
              this.diagnostic.switchToSettings();
            }
          }
        ]
      })
      .present();
  }

  //app
  private assertLocationService = (() => {
    return () => {
      return Observable.create(observer => {
        if (this.enabledLocationService) {
          observer.next(true);
        } else {
          this.diagnostic
            .isLocationEnabled()
            .then(enabled => {
              if (enabled) {
                this.enabledLocationService = true;
                observer.next(true);
              } else {
                this.enabledLocationService = false;
                this.alertCtrl
                  .create({
                    title: "",
                    subTitle: "",
                    buttons: [
                      { text: "" },
                      {
                        text: "",
                        handler: () => {
                          this.diagnostic.switchToLocationSettings();
                        }
                      }
                    ]
                  })
                  .present();
                observer.error(false);
              }
            })
            .catch(err => {
              console.warn(err, "diagnostic.isLocationEnabled");
              observer.error(false);
            });
        }
      });
    };
  })();

  //app
  private assertLocationAuthorization = (() => {
    return () => {
      return Observable.create(observer => {
        if (this.locationAuthorization) {
          observer.next(true);
        } else {
          this.diagnostic
            .isLocationAuthorized()
            .then(res => {
              if (res) {
                this.locationAuthorization = true;
                observer.next(true);
              } else {
                this.locationAuthorization = false;
                this.diagnostic
                  .requestLocationAuthorization("always")
                  .then(res => {
                    //
                    if (res == "DENIED_ALWAYS") {
                      //,
                      this.locationAuthorization = false;
                      this.alertCtrl
                        .create({
                          title: "",
                          subTitle: "app",
                          buttons: [
                            { text: "" },
                            {
                              text: "",
                              handler: () => {
                                this.diagnostic.switchToSettings();
                              }
                            }
                          ]
                        })
                        .present();
                      observer.error(false);
                    } else {
                      this.locationAuthorization = true;
                      observer.next(true);
                    }
                  })
                  .catch(err => {
                    console.warn(
                      err,
                      "diagnostic.requestLocationAuthorization"
                    );
                    observer.error(false);
                  });
              }
            })
            .catch(err => {
              console.warn(err, "diagnostic.isLocationAvailable");
              observer.error(false);
            });
        }
      });
    };
  })();
}



