import { Component, SimpleChanges } from '@angular/core';
import { NavController } from 'ionic-angular';
import { LightApp, LightProviders } from '../../light/light-out/light-out.module';
import { TabsServices } from '../common/tabs/tabs.service';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html',

})
export class HomePage {
  showIconText: number;
  imageUrl: string;
  rowLabel: string;

  public demandPoints: any;
  public layerdata: any;

  Todoflag: boolean = false;
  Doingflag: boolean = false;
  showTodoIconText: number;
  showDingIconText: number;

  constructor(
    public navCtrl: NavController,
    public providers: LightProviders
  ) {
    //super(navCtrl, providers);
  }

  ngOnInit() {
    //
    this.providers.lightHttp.get("Flow/GetToDoCount").then(data => {
      console.log(data);
      if (data > 0) {
        this.Todoflag = true;
        this.showTodoIconText = data;
      } else {
        this.Todoflag = false;
      }

    })
    //
    this.providers.lightHttp.get("Flow/GetDoingFlowCount").then(data => {
      if (data > 0) {
        this.Doingflag = true;
        this.showDingIconText = data;
      } else {
        this.Doingflag = false;
      }

    })

  }

  ionViewDidEnter() {

    this.providers.lightHttp.get("Flow/GetToDoCount").then(data => {
      console.log(data);
      if (data > 0) {
        this.Todoflag = true;
        this.showTodoIconText = data;
      } else {
        this.Todoflag = false;
      }

    })
    //
    this.providers.lightHttp.get("Flow/GetDoingFlowCount").then(data => {
      if (data > 0) {
        this.Doingflag = true;
        this.showDingIconText = data;
      } else {
        this.Doingflag = false;
      }

    })

  }

  todoProcess() {
    let tabsctrl = TabsServices.getInstance();
    let tab = tabsctrl.getByIndex(1);
    TabsServices.setTabParams('todoProcess');
    tabsctrl.select(tab);
  }

  DoingFlow() {
    let tabsctrl = TabsServices.getInstance();
    let tab = tabsctrl.getByIndex(1);
    TabsServices.setTabParams('processing');
    tabsctrl.select(tab);
  }

  CreateFlow() {
    this.navCtrl.push("WorkNewProcessListPage");
  }

  newList() {
    this.navCtrl.push("NewListPage");
  }
  noticeList() {
    this.navCtrl.push("NoticeListPage");
  }
  emailList() {
    this.navCtrl.push("EmailListPage");
  }
  messageList() {
    this.navCtrl.push("MessageListPage");
  }

  /**
   *
   * 
   * @memberof HomePage
   */
  equipmentList() {
    this.navCtrl.push("EquipmentListPage");
  }


  /**
     * 
     */
  exitApp(): void {
    this.providers.lightExit.appExit();
  }

}



