import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import { LightNation } from "../common/lightNation";
import { AndroidPermissions } from "@ionic-native/android-permissions";

/**
 * npm:npm install --save @ionic-native/android-permissions
 * ionic:ionic cordova plugin add cordova-plugin-android-permissions
 * :Android
 * 
 * @export
 * @class LightPermissisons
 * @extends {LightNation}
 */
@Injectable()
export class LightPermissisons extends LightNation {
  constructor(
    private platform: Platform,
    private androidPermissions: AndroidPermissions
  ) {
    super();
  }

  /**
   * 
   *
   * @memberof LightPermissisons
   */
  public requestFilePermission(): Promise<any> {
    var that = this;
    var result = new Promise(function(resolve, reject) {
      if (!that.platform.is("cordova")) {
        resolve();
      } else {
        that.androidPermissions
          .requestPermissions(that._getFilePermissionList())
          .then(value => {
            resolve();
          })
          .catch(errText => {
            reject("" + errText);
          });
        // that._isFilePermissionList().then(value=>{
        //     alert("");
        //     resolve();
        // }).catch(errValue=>{
        //     alert(""+errValue);
        //     that.androidPermissions.requestPermissions(this._getPermissionList()).then(value => {
        //         alert("");
        //         resolve();
        //     }).catch(errText => {
        //         reject(""+errText);
        //     });
        // });
      }
    });
    return result;
  }

  /**
   * 
   *
   * @memberof LightPermissisons
   */
  public requestCameraPermission(): Promise<any> {
    var that = this;
    var result = new Promise<any>(function(resolve, reject) {
      if (!that.platform.is("cordova")) {
        resolve();
      } else {
        that.androidPermissions
          .checkPermission(that._getCameraPermissionValue())
          .then(value => {
            resolve();
          })
          .catch(errText => {
            that.androidPermissions
              .requestPermission(that._getCameraPermissionValue())
              .then(value => {
                resolve();
              })
              .catch(errText => {
                reject("");
              });
          });
      }
    });
    return result;
  }

  /**
   * 
   * @private
   * @returns {string}
   * @memberof LightPermissisons
   */
  private _getCameraPermissionValue(): string {
    return this.androidPermissions.PERMISSION.CAMERA;
  }

  // /**
  //  * 
  //  * @private
  //  * @returns {Promise<any>}
  //  * @memberof LightPermissisons
  //  */
  // private _isFilePermissionList(): Promise<any> {
  //     var that = this;
  //     var resultList = this._getFilePermissionList();
  //     var resultText = resultList[0];
  //     var result = new Promise(function (resolve, reject) {
  //         that.androidPermissions.checkPermission(resultText).then(value => {
  //             resolve();
  //         }).catch(errText => {
  //             reject("" + errText);
  //         });
  //     });
  //     return result;
  // }

  /**
   * 
   * @private
   * @returns {Array<string>}
   * @memberof LightPermissisons
   */
  private _getFilePermissionList(): Array<string> {
    var result = [];
    //
    result.push(this.androidPermissions.PERMISSION.WRITE_EXTERNAL_STORAGE);
    result.push(this.androidPermissions.PERMISSION.READ_EXTERNAL_STORAGE);
    return result;
  }
}



