import { UserObject } from "./UserObject";
import { LightStringUtil } from '../../../light-util/light-util.module';

///
export class OrganizeObject {

    ///
    data: object = null;

    //
    preOrganize: OrganizeObject = null;

    ///
    childOrganize: Array<OrganizeObject> = [];

    ///
    childUser: Array<UserObject> = [];

    constructor(data?: object, organizeObject?: OrganizeObject) {
        this.data = data;
        this.preOrganize = organizeObject;
    }

    ///ID
    get ID(): string {
        var result: string = "-1";
        if (!!this.data) {
            result = this.data["ID"];
        }
        return result;
    }

    ///ID
    get PID(): string {
        var result: string = "-1";
        if (!!this.data) {
            result = this.data["PID"];
        }
        return result;
    }

    ///
    get Name(): string {
        var result: string = "";
        if (!!this.data) {
            result = this.data["Name"];
        }
        return result;
    }

    /**
     * 
     * @returns {boolean} 
     * 
     * @memberof OrganizeObject
     */
    isRoot(): boolean {
        var result: boolean = false;
        if (this.ID == "-1") {
            result = true;
        }
        return result;
    }

    /**
     * 
     * 
     * @param {string} ID 
     * @returns {OrganizeObject} 
     * 
     * @memberof OrganizeObject
     */
    getOrganizeObject(ID: string): OrganizeObject {
        var result: OrganizeObject = null;
        if (!!ID) {
            if (this.childOrganize.length > 0) {
                for (var index = 0; index < this.childOrganize.length; index++) {
                    var element: OrganizeObject = this.childOrganize[index];
                    if (element.ID == ID) {
                        result = element;
                        break;
                    } else {
                        result = element.getOrganizeObject(ID);
                        if (!!result) {
                            break;
                        }
                    }
                }
            }
        }
        return result;
    }

    /**
    * @param {String} ID
    */
    getPreOrganizeList(): Array<OrganizeObject> {
        var result: Array<OrganizeObject> = [];
        result.push(this);
        var organzieTemp = this.preOrganize;
        while (!!organzieTemp) {
            result.unshift(organzieTemp);
            organzieTemp = organzieTemp.preOrganize;
        }
        return result;
    }

    /**
     * 
     * 
     * @param {string}  
     * @returns {Array<UserObject>} 
     * 
     * @memberof OrganizeObject
     */
    getSearchUserList(userName: string): Array<UserObject> {
        var result = [];
        if (!!userName) {
            if (this.childUser.length > 0) {
                for (var m = 0; m < this.childUser.length; m++) {
                    var userTemp = this.childUser[m];
                    if (LightStringUtil.like(userTemp.Name, userName)) {
                        result.push(userTemp);
                    }
                }
            }
            if (this.childOrganize.length > 0) {
                for (var i = 0; i < this.childOrganize.length; i++) {
                    var organizeTemp = this.childOrganize[i];
                    var userList = organizeTemp.getSearchUserList(userName);
                    if (!!userList && userList.length > 0) {
                        userList.forEach(element => {
                            result.push(element);
                        });
                    }
                }
            }

        }
        return result;
    }

    /**
     * 
     * 
     * @param {string}  
     * @returns {Array<OrganizeObject>} 
     * 
     * @memberof OrganizeObject
     */
    getSearchOrganizeList(userName: string): Array<OrganizeObject> {
        var result = [];
        if (!!userName) {
            if (this.childOrganize.length > 0) {
                for (var i = 0; i < this.childOrganize.length; i++) {
                    var organizeTemp = this.childOrganize[i];
                    if (LightStringUtil.like(organizeTemp.Name, userName)) {
                        result.push(organizeTemp)
                    }
                    var organizeList = organizeTemp.getSearchOrganizeList(userName);
                    if (!!organizeList && organizeList.length > 0) {
                        organizeList.forEach(element => {
                            result.push(element);
                        });
                    }
                }
            }

        }
        return result;
    }

}


