/**
* 
* 
* @export
* @class LightStringAppend
*/
export class LightStringAppend {

    stringArray: Array<string>;

    constructor() {
        this.stringArray = new Array<string>();
    }

    /**
     * 
     * 
     * @param {string} value 
     * @memberof LightStringAppend
     */
    appendString(value: string): void {
        if (!!value) {
            this.stringArray.push(value);
        }
    }

    /**
     * 
     * 
     * @param {number}  
     * @memberof LightStringAppend
     */
    appendNumber(value: number): void {
        this.stringArray.push(value + "");
    }

    /**
    * Boolean
    * 
    * @param {number}  
    * @memberof LightStringAppend
    */
    appendBoolean(value: boolean): void {
        this.stringArray.push(value + "");
    }

    /**
     * 
     * @returns {string} 
     * 
     * @memberof LightStringAppend
     */
    toString(joinString?: string): string {
        if (!!!joinString) {
            return this.stringArray.join("");
        }
        else {
            return this.stringArray.join(joinString);
        }
    }

    /**
     * 
     * @memberof LightStringAppend
     */
    clear(): void {
        this.stringArray = [];
    }

}


