import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import { LightNation } from "../common/lightNation";
import { AppVersion } from "@ionic-native/app-version";

/**
 * npm:npm install --save @ionic-native/app-version
 * ionic:ionic cordova plugin add cordova-plugin-app-version
 * :Android | iOS | Windows
 * App
 * @memberof LightAppVersion
 */
@Injectable()
export class LightAppVersion extends LightNation {
  constructor(public appVersion: AppVersion, private platform: Platform) {
    super();
  }

  /**
   * App
   *
   * @returns {string}
   * @memberof LightAppVersion
   */
  getAppName(): Promise<any> {
    var result: Promise<any> = this._getDefaultResult("My Awesome App");
    if (this.platform.is("cordova")) {
      result = this.appVersion.getAppName();
    }
    return result;
  }

  /**
   * 
   * @returns {string}
   * @memberof LightAppVersion
   */
  getPackageName(): Promise<any> {
    var result: Promise<any> = this._getDefaultResult(
      "com.example.myawesomeapp"
    );
    if (this.platform.is("cordova")) {
      result = this.appVersion.getPackageName();
    }
    return result;
  }

  /**
   * 
   * @returns {Promise<any>}
   * @memberof LightAppVersion
   */
  getVersionCode(): Promise<any> {
    var result: Promise<any> = this._getDefaultResult("0.0.0");
    if (this.platform.is("cordova")) {
      result = this.appVersion.getVersionCode();
    }
    return result;
  }

  /**
   * 
   * @returns {Promise<any>}
   * @memberof LightAppVersion
   */
  getVersionNumber(): Promise<any> {
    var result: Promise<any> = this._getDefaultResult("0.0.0");
    if (this.platform.is("cordova")) {
      result = this.appVersion.getVersionNumber();
    }
    return result;
  }
}



