import { Injectable } from '@angular/core';
import { LightHttp } from '../../light-providers/light-providers.module';
import 'rxjs/add/operator/toPromise';

/**
 * 
 * 
 * @export
 * @class LightWriterService
 */
@Injectable()
export class LightReadService {

    /**
     * 
     * @memberof LightWriterService
     */
    constructor(
        public lightHttp: LightHttp
    ) {

    }

    /**
     * 
     * @param {string} objectID 
     * @param {string} step 
     * @returns 
     * @memberof LightWriterService
     */
    public getReadForm(objectID: string): Promise<any> {
        return this.lightHttp.get("/Flow/GetFlowForm", { objectID: objectID });
    }

}


