import { Injectable } from '@angular/core';
import { DES, enc, mode, pad } from 'crypto-js';

@Injectable()
export class LightDES {

    /**
     * DES
     * :npm install --save @ionic-native/app-version
     * @memberof lightDES
     */
    constructor(

    ) {

    }

    //
    encryptDESFn(cipherText: any, key: any = 'U2FsdGVk') {

        return cipherText = DES.encrypt(enc.Utf8.parse(cipherText), enc.Utf8.parse(key), {
            iv: enc.Utf8.parse(key),
            mode: mode.CBC,
            padding: pad.Pkcs7
        }).toString();
    }

    //
    decryptDESFn(cipherText: any, key: any = 'U2FsdGVk') {
        var bytes = DES.decrypt(cipherText, enc.Utf8.parse(key), {
            iv: enc.Utf8.parse(key),
            mode: mode.CBC,
            padding: pad.Pkcs7
        });
        var originalText = bytes.toString(enc.Utf8);
        return originalText;
    }
}




