import { Tabs } from 'ionic-angular/components/tabs/tabs';

export class TabsServices {
    private static tabInstance: Tabs;
    private static tabParams:any;
    constructor(){

    }

    public static setTabsInstance(instance: Tabs):void{
        this.tabInstance = instance;
    }
    public static IsHasinstance():boolean {
        return !!this.tabInstance;
    }
    public static getInstance():Tabs{
        return this.tabInstance;
    }
    public static setTabParams(obj:any):void{
        this.tabParams = obj;
    }
    public static getTabParams():any{
        let result = this.tabParams;
        this.tabParams = null;
        return result;
    }
}


