import { IFormAction } from './IFormActive';
import { FormActionBase } from './FormActionBase';

/**
 * 
 * @export
 * @interface IFormAction
 */
export class FormListAction extends FormActionBase implements IFormAction {

    ///
    private _headVisableKey: string = "headvisiable";

    ///
    private _displayVisableKey: string = "displayvisiable";

    ///
    headVisable: boolean = false;

    ///
    displayVisable: boolean = false;

    constructor(
        dataObject: any
    ) {
        super(dataObject);
        if (!!dataObject) {
            this.headVisable = dataObject[this._headVisableKey];
            this.displayVisable = dataObject[this._displayVisableKey];
        }
    }

}


