import { Injectable } from '@angular/core';
import { NavController, App, ViewController } from 'ionic-angular';
import { LightProviders } from '../../light-providers/light-providers.module';
import { LightJsonUtil, LightStringUtil } from '../../light-util/light-util.module';
import { LightAppVersion } from '../../light-nation/light-nation.module';

/**
 * App
 *
 * @export
 * @class LightApp
 */
@Injectable()
export class LightApp {

    // //
    // updateFlag: boolean = false;

    // //
    // appVersion: string = "0.0.1";

    // ///Url
    // upgradeURL: string = "";

    /**
     * 
     * @param {LightProviders} lightProviders
     * @memberof LightLogin
     */
    constructor(
        public lightProviders: LightProviders,
        public lightAppVersion: LightAppVersion,
    ) {

    }

    /**
     * 
     *
     * @param {string} userName  
     * @param {string} userPassword 
     * @param {*} page 
     * @memberof LightLogin
     */
    loginToPage(navCtrl: NavController, userName: string, userPassword: string, page: any): void {
        var that = this;
        if (!!userName && !!userPassword) {
            this.lightProviders.lightUser.userLogin(userName, userPassword).then(() => {
                if (!!page) {
                    that.lightProviders.lightNav.push(navCtrl, page);
                    // navCtrl.push(page)
                    that.lightProviders.lightNav.setRootPage(page);
                }
            }).catch(errText => {
                if (LightStringUtil.contain(errText, "")) {
                    this.lightProviders.lightPop.message(errText);
                } else {
                    this.lightProviders.lightPop.message("");
                }
            });
        } else {
            this.lightProviders.lightPop.message("");
        }
    }
    updateAppFn(isUpdate: boolean = true, tipShow: boolean = true, listener: (event: ProgressEvent) => any = null) {
        //
        console.log("")
    }

    /**
     * 
     */
    logoutPage(page: any, app: App) {
        this.lightProviders.lightPop.confirm("").then((resultText) => {
            console.log(resultText);
            app.getRootNav().setRoot(page);
        }).catch((errText) => {
            console.log(errText);
        });
    }

    /**
     * 
     */
    hideBackButton(viewCtrl: ViewController) {
        viewCtrl.showBackButton(false);
    }

}



