import { Injectable } from '@angular/core';
import { Http, RequestOptions, Headers, URLSearchParams, RequestMethod } from '@angular/http';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';
import 'rxjs/add/operator/timeout';
import { LightConfig } from './lightConfig';

//
const failureFlag: string = "Failure:";
//OK
const successFlag: string = "OK:";
//urlhttp
const httpUrlFlag: string = "http://";

@Injectable()
export class LightHttp {

    /**
     * 
     * 1.loading
     * 2.
     * @memberof LightHttp
     */

    /**
     * 
     * @memberof LightHttp
     */
    private static defaultParam = null;

    /**
     * 
     * @param {Http} http 
     * @param {LightConfig} lightConfig 
     * @memberof LightHttp
     */
    constructor(
        private http: Http,
        private lightConfig: LightConfig
    ) {

    }

    //get
    get(url: string, opt?: object): Promise<any> {
        var that = this;
        url = this._httpUrlFilter(url);
        var result: Promise<any> = new Promise(function (resolve, reject) {
            that._get(url, opt).then(value => {
                var resultFlag = that._isErr(value);
                if (!resultFlag[0]) {
                    resolve(resultFlag[1]);
                } else {
                    reject(resultFlag[1]);
                }
            }).catch(errText => {
                that._httpUrlErr();
                reject("");
            });
        });
        return result;
    }

    //Post
    post(url: string, opt?: object): Promise<any> {
        //return this.get(url,opt);
        var that = this;
        url = this._httpUrlFilter(url);
        // console.log("url",url);
        var result: Promise<any> = new Promise(function (resolve, reject) {
            that._post(url, opt).then(value => {
                var resultFlag = that._isErr(value);
                if (!resultFlag[0]) {
                    resolve(resultFlag[1]);
                } else {
                    reject(resultFlag[1]);
                }
            }).catch(errText => {
                that._httpUrlErr();
                reject("");
            });
        });
        return result;
    }

    /**
     * Url
     * @param {string} url 
     * @returns {string} 
     * @memberof LightHttp
     */
    getUrl(url: string): string {
        return this._httpUrlFilter(url);;
    }

    /**
     * 
     * @param {string} key 
     * @param {*} value 
     * @memberof LightHttp
     */
    setDefaultParam(key: string, value: any): void {
        if (!!key) {
            if (!!!LightHttp.defaultParam) {
                LightHttp.defaultParam = {};
            }
            LightHttp.defaultParam[key] = value;
        }
    }

    //
    private _httpUrlErr(): void {
        console.log("");
    }

    //url
    private _httpUrlFilter(urlText: string): string {
        var result: string = urlText;
        if (!!result && !!this.lightConfig.baseUrl) {
            if (result.indexOf(httpUrlFlag) < 0) {
                result = this.lightConfig.baseUrl + result;
            }
        }
        return result;
    }

    //
    private _isErr(resultText: string): [boolean, string] {
        var result: [boolean, string] = [false, resultText];
        if (!!resultText) {
            if (resultText.indexOf(failureFlag) >= 0) {
                result[0] = true;
            }
            if (resultText.indexOf(successFlag) >= 0) {
                result[1] = resultText.substr(successFlag.length, resultText.length - successFlag.length);
            }
        }
        return result;
    }

    //get
    private _get(url: string, opt?: object): Promise<any> {
        var params: URLSearchParams = this._getSearchParams(opt);
        return this.http.get(url, { search: params }).timeout(this.lightConfig.httpTimeOut).map(res => {
            var targetText: string = res.text();
            //console.log(targetText);
            return targetText;
        }).toPromise();
    }

    //
    private _getSearchParams(opt?: object): URLSearchParams {
        var params: URLSearchParams = new URLSearchParams();
        opt = this._checkFilterParams(opt);
        if (!!opt) {
            for (var key in opt) {
                params.set(key, opt[key]);
            }
        }
        return params;
    }

    //post
    private _post(url: string, opt?: object): Promise<any> {
        //let body = JSON.stringify(opt);
        let body = this._getFromParams(opt);
        let headers = new Headers();
        headers.append("Content-Type", "application/x-www-form-urlencoded");
        //headers.append("Access-Control-Allow-Origin","*");
        let options = new RequestOptions({ headers: headers, method: RequestMethod.Post });
        return this.http.post(url, body, options).timeout(this.lightConfig.httpTimeOut).map(res => {
            var targetText: string = res.text();
            return targetText;
        }).toPromise();
    }

    /**
     * 
     * 
     * @private
     * @param {object} [opt] 
     * @returns {string} 
     * 
     * @memberof LightHttp
     */
    private _getFromParams(opt?: object): string {
        var result: string = "";
        opt = this._checkFilterParams(opt);
        if (!!opt) {
            for (var key in opt) {
                result = result + key + "=" + opt[key] + "&";
            }
        }
        return result;
    }

    private _checkFilterParams(opt?: object): any {
        var result = null;
        if (!!opt) {
            if (!!!result) {
                result = {};
            }
            for (var key in opt) {
                result[key] = opt[key];
            }
        }

        if (!!LightHttp.defaultParam) {
            if (!!!result) {
                result = {};
            }
            for (var i in LightHttp.defaultParam) {
                result[i] = LightHttp.defaultParam[i];
            }
        }
        return result;
    }

}




