import { Injectable } from '@angular/core';
import { NavController, App, NavParams } from 'ionic-angular';
import { LightStringUtil, LightFunctionUtil } from '../../light-util/light-util.module';

//
@Injectable()
export class LightNav {

    /**
     * 
     * @private
     * @type {*}
     * @memberof LightNav
     */
    private _rootPage: any;

    /**
     * 
     * @param {LightNav} nav
     * @memberof LightNav
     */
    constructor(
        public app: App,
    ) {

    }

    /**
     * 
     * @memberof LightNav
     */
    public setRootPage(rootPage: any): void {
        this._rootPage = rootPage;
    }

    /**
     * 
     */
    public getRootPage(): any {
        return this._rootPage;
    }

    /**
     * 
     *
     * @param {*} page
     * @param {*} data
     * @memberof LightNav
     */
    public push(navCtrl: NavController, page: any, data: any = null): void {
        if (LightStringUtil.isString(page)) {
            this._pushStringView(navCtrl, page, data);
        }
        else if (LightFunctionUtil.isFunction(page)) {
            this._pushFunctionView(navCtrl, page, data);
        }
        else {

        }
    }

    /**
     * 
     *
     * @private
     * @param {*} page
     * @param {*} [data=null]
     * @memberof LightNav
     */
    private _pushStringView(navCtrl: NavController, page: any, data: any = null): void {
        var lastView = this._getLastView(navCtrl);
        var flag: boolean = false;
        if (!!lastView && lastView.name == page) {
            flag = true;
        }
        if (!flag) {
            navCtrl.push(page, data);
        }
    }

    /**
     * 
     *
     * @private
     * @param {*} page
     * @param {*} [data=null]
     * @memberof LightNav
     */
    private _pushFunctionView(navCtrl: NavController, page: any, data: any = null): void {
        var lastView = this._getLastView(navCtrl);
        var flag: boolean = false;
        if (!!lastView && lastView.name == LightFunctionUtil.getFunctionName(page)) {
            flag = true;
        }
        if (!flag) {
            navCtrl.push(page, data);
        }
    }

    /**
     * View
     *
     * @private
     * @returns {*}
     * @memberof LightNav
     */
    private _getLastView(navCtrl: NavController): any {
        var result: any = null;
        var viewArray = navCtrl.getViews();
        if (!!viewArray && viewArray.length > 1) {
            result = viewArray[viewArray.length - 1];
        }
        return result;
    }

    /**
     * Back
     * @param {*} page
     * @param {*} [data=null]
     * @memberof LightNav
     */
    public back(navCtrl: NavController, page: any, data: any = null): void {
        if (LightStringUtil.isString(page)) {
            this._backStringView(page, data);
        }
        else if (LightFunctionUtil.isFunction(page)) {
            this._backFunctionView(page, data);
        }
        else {

        }
    }

    /**
     * 
     *
     * @private
     * @param {*} page
     * @param {*} [data=null]
     * @memberof LightNav
     */
    private _backStringView(navCtrl: NavController, page: any, data: any = null): void {
        navCtrl.popTo(page, data);
    }

    /**
     * 
     *
     * @private
     * @param {*} page
     * @param {*} [data=null]
     * @memberof LightNav
     */
    private _backFunctionView(navCtrl: NavController, page: any, data: any = null): void {
        navCtrl.popTo(page, data);
    }

    /**
     * 
     * @memberof LightNav
     */
    public backToRoot(navCtrl: NavController): void {
        if (!!navCtrl) {
            navCtrl.popToRoot();
        }
    }

    // 
    private pageParams: {
        curIndex: number,
        curParams: NavParams
    }[] = [];

    // 
    public getParams(navCtrl: NavController): NavParams {
        let curParams = navCtrl.getActive().getNavParams();
        let curIndex = navCtrl.indexOf(navCtrl.getActive());
        console.log(this.pageParams, curParams, curIndex);
        if((Object.keys(curParams.data).indexOf('tabId') >= 0) || Object.keys(curParams.data).length === 0) {
            // 
            let tempParams = this.pageParams.find((v) => {
              return v.curIndex == curIndex;
            });
            return (typeof tempParams != 'undefined') ? tempParams.curParams : curParams;
          } else {
            // 
            this.pageParams = this.pageParams
                .filter((v) => {
                    // 
                    if(v.curIndex < curIndex)
                    return v;
                }).concat([
                    // 
                    {curIndex, curParams}
                ]);
            return curParams;
        };
    }

}



