import { Component, OnInit, Input } from '@angular/core';
import { LightWriterCommon } from '../common/index';

@Component({
    //moduleId: module.id,
    selector: 'light-writer-div',
    templateUrl: 'light-writer-div.component.html',
    //styleUrls: ['light-writer-div.component.scss']
})
export class LightWriterDivComponent extends LightWriterCommon implements OnInit {

    ifShow = true;

    /**
     * Creates an instance of LightWriterDivComponent.
     * @memberof LightWriterDivComponent
     */
    constructor(

    ) {
        super();
    }

    /**
     *
     * 
     * @memberof LightReadDivComponent
     */
    public ngOnInit(): void {
        super.ngOnInit();
        if('ifShow' in this.getExtendObject()) {
            this.ifShow = this.getExtendObject()['ifShow'];
        }
    }

}



