import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { UserObject } from '../common/UserObject';
import { OrganizeObject } from '../common/OrganizeObject';

@Component({
    //moduleId: module.id,
    selector: 'contact-content',
    templateUrl: 'contact-content.component.html',
    //styleUrls: ['contact-content.component.scss']
})
export class ContactContentComponent implements OnInit {

    ///
    @Input() childOrganizeList: Array<OrganizeObject> = [];

    ///
    @Input() childUserList: Array<UserObject> = [];

    ///
    @Output() selectOrganizeEvent = new EventEmitter<OrganizeObject>();

    ///
    @Output() selectUserEvent = new EventEmitter<UserObject>();

    ///
    constructor(

    ) {

    }

    ngOnInit(): void {

    }

    /**
     * 
     * @param {OrganizeObject}  
     * @memberof ContactContent
     */
    selectOrganize(organzieObject: OrganizeObject): void {
        this.selectOrganizeEvent.emit(organzieObject);
    }

    /**
     * 
     * @param {organzieObject} 
     * @memberof ContactContent
     */
    selectUser(organzieObject: UserObject): void {
        this.selectUserEvent.emit(organzieObject);
    }

}



