import { Component, Input, OnInit } from '@angular/core';
import { IFormNode } from '../../light-common/index';

@Component({
    selector: 'light-read-text',
    templateUrl: 'light-read-text.component.html'
    // styleUrls: ['light-read-text.component.scss']
})
export class LightReadTextComponent implements OnInit {

    //
    @Input() formRow: IFormNode = null;

    //
    rowType: string = "";

    //
    title: string = "";

    //
    value: String = "";

    /**
     * 
     * 
     * @memberof LightReadDivComponent
     */
    public ngOnInit(): void {
        this.rowType = this.formRow.detailType;
        if (!!!this.rowType) {
            this.rowType = "";
        }
        this.title = this.formRow.title;
        this.value = this.formRow.value;
    }
}



