import { Injectable } from '@angular/core';
import { AlertController } from 'ionic-angular';
import 'rxjs/add/operator/map';

//
@Injectable()
export class LightPop {

    //
    autoTimeClose: number = 2500;

    //
    constructor(
        public alertCtrl: AlertController
    ) {

    }

    /**
     * 
     * @param {string}  
     * @param {string}  
     * 
     * @memberof LightPop
     */
    message(message: string, title?: string, autoClose: boolean = true): Promise<any> {
        title = this._checkTitle(title);
        var that = this;
        var result: Promise<any> = new Promise(function (resolve, reject) {
            var alert = that.alertCtrl.create({
                title: title,
                subTitle: message,
                buttons: [{
                    text: '',
                    handler: () => {
                        resolve(true);
                    }
                }]
            });
            alert.present();
            if (autoClose) {
                setTimeout(function () {
                    alert.dismiss();
                }, that.autoTimeClose);
            };
        });
        return result;
    }

    /**
     * 
     * @param {string} message 
     * @param {string} [title] 
     * @memberof LightPop
     */
    errMessage(message: string, title?: string): void {
        this.message(message, title, false);
    }

    /**
     * 
     * 
     * @param {string}  
     * @param {string}  
     * 
     * @memberof LightPop
     */
    confirm(message: string, title?: string): Promise<any> {
        title = this._checkTitle(title);
        var that = this;
        var result: Promise<any> = new Promise(function (resolve, reject) {
            var alert = that.alertCtrl.create({
                title: title,
                subTitle: message,
                buttons: [{
                    text: '',
                    handler: () => {
                        resolve(true);
                    }
                }, {
                    text: '',
                    handler: () => {
                        reject(false);
                    }
                }]
            });
            alert.present();
        });
        return result;
    }

    /**
     * 
     * 
     * @private
     * @param {string} title 
     * @returns {string} 
     * 
     * @memberof LightPop
     */
    private _checkTitle(title: string): string {
        var result: string = title;
        if (!!!title) {
            title = "";
        }
        return result;
    }

}



