import { Component, OnInit } from '@angular/core';
import { LightWriterCommon } from '../../common/index';

@Component({
    //moduleId: module.id,
    selector: 'writer-muti',
    templateUrl: 'writer-muti.component.html',
    //styleUrls: ['writer-muti.component.scss']
})
export class WriterMutiComponent extends LightWriterCommon implements OnInit {

    //
    dataList: Array<any> = [];

    private _content: any = null;

    //
    oldContent: any;

    public get content(): any {
        return this._content;
    }

    public set content(value: any) {
        let arrayList = value as Array<any>;
        this.formRow.value = arrayList.join(",");
        this._content = value;
    }


    /**
     * 
     * 
     * @memberof WriterCheckComponent
     */
    public ngOnInit(): void {
        super.ngOnInit();
        this.dataList = this.getExtendObjectValue("enumeration");
        this.oldContent = this.formRow.value;
    }

    /**
     * 
     * @memberof WriterMutiComponent
     */
    public dataSelect(): void {
        // console.log("",this.content,this.dataList);
        //var arrayList=this.content.split(",");
        //this.formRow.value=arrayList.join('');
    }

    /**
     * 
     * 
     * @memberof WriterMutiComponent
     */
    public ionCancel(): void {
        this.formRow.value = this.oldContent;
    }

}



