import { LightWriterService } from './../../light-writer.service';
import { Component, OnInit, Input } from '@angular/core';
import { LightWriterCommon } from '../../common/index';
import { Observable } from 'rxjs/Observable';
import { Subscriber } from 'rxjs/Subscriber';

@Component({
  selector: 'writer-toggle',
  templateUrl: 'writer-toggle.component.html'
})
export class WriterToggleComponent extends LightWriterCommon implements OnInit{

    observer: Subscriber<any>;
    observable: Observable<any>;

    /**
     * 
     * @memberof WriterToggleComponent
     */
    constructor(
        public lightWriterService: LightWriterService
    ) {
        super();
    }

    ngOnInit() {}

    onToggle() {
      let relateList = this.getExtendObject()['relateFieldList'];
      let writerData = this.lightWriterService.writerData;
      writerData.map(item => {
        if(relateList.indexOf(item['extend']['field']) != -1) {
            item.extend['ifShow'] = this.formRow.value;
        }
      })
      this.lightWriterService.observer.next(writerData);
    }

}



