import { Injectable } from '@angular/core';
import { LightLocalCache } from './lightLocalCache';

@Injectable()
export class LightConfig {

    //Url
    private _baseUrl: string = "";

    //urlkey
    private _baseUrlKey: string = "baseUrl";

    //
    timeOut: number = 10000;

    //
    httpTimeOut: number = 60000;

    /**
     * 
     * @param {LightLocalCache} localCache 
     * @memberof LightConfig
     */
    constructor(
        private localCache: LightLocalCache
    ) {

    }

    /**
     * url
     * 
     * @type {string}
     * @memberof LightConfig
     */
    public get baseUrl(): string {
        var result: string = "";
        if (!!this._baseUrl) {
            result = this._baseUrl;
        } else {
            result = this.localCache.get(this._baseUrlKey);
        }
        return result;
    }

    /**
     * Url
     * @memberof LightConfig
     */
    public set baseUrl(value: string) {
        if (!!value) {
            this._baseUrl = value;
            this.localCache.set(this._baseUrlKey, value);
        }
    }

}


