import { Component, OnInit } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { LightWriterFlow } from '../../light-writer/index';
import { LightHttp, LightProviders } from '../../../light-providers/light-providers.module';
import { LightJsonUtil, LightStringUtil } from '../../../light-util/light-util.module';
import { BackShowStepComponent } from '../back-show-step/back-show-step.component';
import { BackSubmitStepComponent } from '../back-submit-step/back-submit-step.component';
import { LightFlowService } from '../light-flow.service';
import { IonicPage } from 'ionic-angular/navigation/ionic-page';

@IonicPage({
  segment: 'work-detail/light-flow-back'
})
@Component({
    selector: 'light-flow-back',
    templateUrl: 'light-flow-back.component.html'
})
export class LightFlowBackComponent implements OnInit {

    /**
     * 
     * @type {LightWriterFlow}
     * @memberof LightFlowBackComponent
     */
    writerFlow: LightWriterFlow = null;

    /**
     * 
     * @type {*}
     * @memberof LightFlowBackComponent
     */
    preStepObject: any = null;

    ///ID
    backToStepID: string = "()";

    ///
    backList: Array<string> = [];

    ///
    backListResult: Array<string> = [];

    ///ID
    submitToStepID: string = "";

    ///
    reason: string = "";

    ///
    submitList: Array<any> = [];

    ///
    submitListResult: Array<string> = [];

    /**
     * 
     * @param navCtrl
     * @param navParam
     * @param lightProviders
     * @param lightHttp
     */
    constructor(
        public navCtrl: NavController,
        public navParam: NavParams,
        public lightProviders: LightProviders,
        public lightHttp: LightHttp
    ) {
        this.writerFlow = this.navParam.data.flow as LightWriterFlow;
    }

    /**
     * 
     *
     * @memberof LightFlowBackComponent
     */
    public ngOnInit(): void {
        if (!!this.writerFlow) {
            //
            this.lightHttp.get("flow/GetPreStep", { objectID: this.writerFlow.objectID, curStepID: this.writerFlow.getCurStepID() }).then(value => {
                this.preStepObject = LightJsonUtil.toObject(value);
                this.backToStepID = this.preStepObject.Title + "()";
                this.backList.push(this.backToStepID);
                this.lightHttp.get("Flow/GetBackToStepList", { objectID: this.writerFlow.objectID, curStepID: this.writerFlow.getCurStepID() }).then(result => {
                    this.backListResult = LightJsonUtil.toObject(result) as Array<any>;
                    this.backListResult.forEach(element => {
                        if (element["ID"] != this.preStepObject["ID"]) {
                            this.backList.push(element["Title"]);
                        }
                    });
                })
            }).catch(errText => {
                this.lightProviders.lightPop.errMessage(errText);
                this.navCtrl.pop();
            });
        }
    }

    /**
     * 
     *
     * @memberof LightFlowBackComponent
     */
    public showBackStepModal(): void {
        this.lightProviders.lightModal.push(BackShowStepComponent, { data: this.backList }, value => {
            if (!!value) {
                this.backToStepID = value;
            }
        });
    }

    /**
     *
     * 
     *
     * @memberof LightFlowBackComponent
     */
    public showSubimitModal(): void {

        this.lightProviders.lightHttp.get("Flow/GetSubmitToStepList", { stepID: this._getSelectStepID(this.backToStepID) }).then(value => {
            this.submitList = [];
            this.submitListResult = LightJsonUtil.toObject(value) as Array<any>;
            this.submitList.push({ ID: this.submitToStepID, Title: this.submitToStepID });
            this.submitListResult.forEach(element => {
                this.submitList.push(element);
            });
            this.lightProviders.lightModal.push(BackSubmitStepComponent, { data: this.submitList }, value => {
                if (!!value) {
                    this.submitToStepID = value;
                }
            });
        });
    }

    /**
     * 
     * @private
     * @returns {string}
     * @memberof LightFlowBackComponent
     */
    private _getSelectStepID(select: string): string {
        var result: string = "";
        if (LightStringUtil.contain(select, "()")) {
            result = this.preStepObject.ID;
        } else {
            this.backListResult.forEach(element => {
                if (element["Title"] == select) {
                    result = element["ID"];
                }
            });
        }
        return result;
    }

    /**
    * 
    * @private
    * @returns {string}
    * @memberof LightFlowBackComponent
    */
    private _getSubmitStepID(): string {
        var result: string = "";
        if (this.submitToStepID == "") {
            result = "";
        } else {
            result = this.submitToStepID;
        }
        return result;
    }

    /**
     * 
     * @memberof LightFlowBackComponent
     */
    public submitTaskDetail(): void {
        this.lightProviders.lightHttp.get("Flow/BackToFlow", { objectID: this.writerFlow.objectID, backToStepID: this._getSelectStepID(this.backToStepID), submitToStepID: this._getSubmitStepID(), curStepID: this.writerFlow.getCurStepID(), reason: this.reason }).then(value => {
            LightFlowService.flowDoneFlag = true;
            this.lightProviders.lightNav.backToRoot(this.navCtrl);
            this.lightProviders.lightPop.message("");
        }).catch(errText => {
            this.lightProviders.lightPop.errMessage(errText);
        });
    }

}



