///
export class UserObject {

    //
    data: object = null;

    //ID
    organizeID: string = null;

    //
    organizeName: string = null;

    constructor(
        data: object,
        organizeName?: string,
        organizeID?: string
    ) {
        this.data = data;
        this.organizeName = organizeName;
        this.organizeID = organizeID;
    }

    //
    get OrganizeName(): string {
        return this.organizeName;
    }

    //ID
    get ID(): string {
        var result: string = "-1";
        if (!!this.data) {
            result = this.data["ID"];
        }
        return result;
    }

    //ID
    get PID(): string {
        var result: string = "-1";
        if (!!this.data) {
            result = this.data["PID"];
        }
        return result;
    }

    //
    get Name(): string {
        var result: string = "";
        if (!!this.data) {
            result = this.data["Name"];
        }
        return result;
    }

    //
    get Title(): string {
        var result: string = "";
        if (!!this.data) {
            result = this.data["Title"];
        }
        return result;
    }

    //
    get Extend(): object {
        var result: object = null;
        if (!!this.data) {
            result = this.data["Extend"];
        }
        return result;
    }

}


