import { Component } from '@angular/core';
import { NavController, NavParams, ViewController } from 'ionic-angular';
import { LightProviders } from '../../../light-providers/light-providers.module';

@Component({
    selector: 'back-show-step',
    templateUrl: 'back-show-step.component.html'
})
export class BackShowStepComponent {

    //
    stepList: Array<any> = [];

    //
    selectStep: string = "";

    /**
     * 
     * @param {NavController} navCtrl 
     * @param {NavParams} navParam 
     * @memberof BackShowStepComponent
     */
    constructor(
        public navCtrl: NavController,
        public navParam: NavParams,
        public viewCtrl: ViewController,
        public lightProviders: LightProviders
    ) {
        var arrayList = this.navParam.data.data as Array<any>;
        if (!!arrayList && arrayList.length > 0) {
            this.selectStep = arrayList[0];
            arrayList.forEach(element => {
                if (this.selectStep == element) {
                    this.stepList.push({ UserName: element, Select: true });
                } else {
                    this.stepList.push({ UserName: element, Select: false });
                }
            });
        }
    }

    /**
     * 
     * @memberof HandleMutiContactComponent
     */
    goBack(): void {
        this.lightProviders.lightModal.dismiss(this.viewCtrl);
    }

    //
    selectClick(value: string): void {
        this.lightProviders.lightModal.dismiss(this.viewCtrl, value);
    }
}



