import { FormFomat } from './FormFomat';

/**
 * 
 * 
 * @export
 * @class FormRowUtil
 */
export class FormRowBaseUtil {

    /**
    * 
    * @static
    * @param {string} type 
    * @returns {boolean} 
    * 
    * @memberof FormRowUtil
    */
    public static isNode(type: string): boolean {
        var result: boolean = false;
        if (FormRowBaseUtil.isStartNode(type) || FormRowBaseUtil.isEndNode(type)) {
            result = true;
        }
        return result;
    }

    /**
    * 
    * @returns {boolean} 
    * @memberof FormRowObject
    */
    public static isStartNode(type: string): boolean {
        var result: boolean = false;
        if (!!type) {
            if (type == FormFomat.GroupStartType || type == FormFomat.TableStartType || type == FormFomat.GroupStartType) {
                result = true;
            }
        }
        return result;
    }

    /**
     * 
     * 
     * @returns {boolean} 
     * 
     * @memberof FormRowObject
     */
    public static isEndNode(type: string): boolean {
        var result: boolean = false;
        if (!!type) {
            if (type == FormFomat.GroupEndType || type == FormFomat.TableEndType || type == FormFomat.GroupEndType) {
                result = true;
            }
        }
        return result;
    }

}


