import { Component, Input, OnInit } from '@angular/core';
import { LightMemberImage } from '../../../light-providers/light-providers.module';

@Component({
  selector: 'member-image',
  templateUrl: 'member-image.component.html'
})
export class MemberImageComponent implements OnInit {

  //
  @Input() detailName: string;

  //
  imageName: string;

  //
  styleObject: object = {};

  //
  constructor(
    public lightMemberImage: LightMemberImage
  ) {

  }

  //
  ngOnInit() {
    if (!!this.detailName) {
      this.imageName = this._getImageName(this.detailName);
      this.styleObject = { 'background-color': this.lightMemberImage.getRandomColor(this.detailName) };
    }
  }

  /**
   * 
   *
   * @private
   * @param {string} 
   * @returns {string}
   *
   * @memberof MemberImage
   */
  private _getImageName(userName: string): string {
    var result: string = "";
    if (!!userName) {
      if (userName.length >= 3) {
        result = userName.substr(userName.length - 2, 2);
      } else {
        result = userName;
      }
    }
    return result;
  }

}



