/**
 * 
 *
 * @export
 * @class LightNumberUtil
 */
export class LightNumberUtil {
  /**
   * 
   * @static
   * @param {number} value
   * @returns {string}
   *
   * @memberof LightNumberUtil
   */
  public static toPercentText(value: number, several: number = 2): string {
    return value.toFixed(several) + "%";
  }

  /**
   * 
   * @param target
   */
  public static isNumber(target: any): boolean {
    var result = false;
    if (!!target && typeof target === "number") {
      result = true;
    }
    return result;
  }
}



