import { Injectable } from '@angular/core';
import { NavController } from 'ionic-angular';

import { LightWriterService, LightWriterFlow } from '../light-writer/index';
import { LightProviders, LightLoading } from '../../light-providers/light-providers.module';
import { LightJsonUtil } from '../../light-util/light-util.module';

import { HandleMutiContactComponent } from './handle-muti-contact/handle-muti-contact.component';
import { HandleMutiPeopleComponent } from './handle-muti-people/handle-muti-people.component';
import { HandleSingleContactComponent } from './handle-single-contact/handle-single-contact.component';
import { HandleSinglePeopleComponent } from './handle-single-people/handle-single-people.component';
import { HandleStepComponent } from './handle-step/handle-step.component';

/**
 * 
 * 
 * @export
 * @class LightFlowService
 */
@Injectable()
export class LightFlowService {

    /**
     * 
     */
    public static flowDoneFlag: boolean = false;

    /**
     * 
     * @param {LightWriterService} writerService 
     * @memberof LightFlowService
     */
    constructor(
        private writerService: LightWriterService,
        private lightProviders: LightProviders,
        private lightLoading: LightLoading
    ) {

    }

    /**
     * 
     * @returns {Promise<any>} 
     * @memberof LightFlowService
     */
    public getStartFlowList(): Promise<any> {
        return this.lightProviders.lightHttp.get("Flow/GetStartFlowList");
    }

    /**
     * 
     * @param {string} sCode 
     * @returns {Promise<LightWriterFlow>} 
     * @memberof LightFlowService
     */
    public getStartFlow(sCode: string): Promise<LightWriterFlow> {
        var that = this;
        var result: Promise<LightWriterFlow> = new Promise<LightWriterFlow>(function (resolve, reject) {
            that.lightProviders.lightHttp.get("Flow/StartFlow", { objectID: sCode }).then(value => {
                var dataStartFlow: any = LightJsonUtil.toObject(value);
                that.getWriterFlow(dataStartFlow.sDocCode).then(content => {
                    resolve(content);
                }).catch(errText => {
                    that.lightProviders.lightPop.errMessage("");
                })
            });
        });
        return result;
    }

    /**
     * 
     * @param {string} objectID 
     * @returns {Promise<LightWriterFlow>} 
     * @memberof LightFlowService
     */
    public getWriterFlow(objectID: string): Promise<LightWriterFlow> {
        return this.writerService.getWriterFlow(objectID);
    }

    /**
     * 
     * 
     * @param {LightWriterFlow} writerFlow 
     * @returns {Promise<any>} 
     * @memberof LightFlowService
     */
    public getWriterForm(writerFlow: LightWriterFlow): Promise<any> {
        return this.writerService.getWriterForm(writerFlow);
    }

    /**
     * 
     * @param {LightWriterFlow} writerFlow 
     * @param {string} writerData 
     * @memberof LightFlowService
     */
    public submitFlow(navCtrl: NavController, writerFlow: LightWriterFlow, writerData: any): void {
        // 
        let loadctrl = this.lightLoading.showLoading('');
        // 
        let SubWriterData = JSON.parse(JSON.stringify(writerData));
        // datetime
        SubWriterData = SubWriterData.map(item => {
            if (item.type == 'datetime') item.value = item.value.replace('T', " ")
            return item
        })
        this.writerService.saveWriterForm(writerFlow, LightJsonUtil.toJson(SubWriterData), true).then(value => {
            this.writerService._getWriterFlowInfoAgain(writerFlow.objectID, writerFlow).then(value => {
                // 
                loadctrl.instance && loadctrl.dismiss();
                var result = false;
                //
                if (writerFlow.isNextStepSingle()) {
                    var userList = writerFlow.getNextStepUserList();
                    if (!!userList && userList.length == 1) {
                        result = true;
                    }
                }
                ///--
                if (result) {
                    this._submitFlow(navCtrl, writerFlow, userList, writerFlow.getNextStepFristID());
                } else {
                    if (!writerFlow.isNextStepSingle()) {
                        //
                        this._submitFlowStep(navCtrl, writerFlow, value);
                    }
                    else {
                        this._submitUserFlow(navCtrl, writerFlow);
                    }
                }
            }).catch(err => {
                // 
                loadctrl.instance && loadctrl.dismiss();
                console.log(err);
            });
        }).catch((err: string) => {
            // 
            loadctrl.instance && loadctrl.dismiss();
            this.lightProviders.lightPop.errMessage(err.substring(8));
        })
    }

    /**
     * 
     * @param navCtrl 
     * @param writerFlow 
     */
    private _submitUserFlow(navCtrl: NavController, writerFlow: LightWriterFlow): void {
        var userList = writerFlow.getNextStepUserList();
        if (!!userList && userList.length == 1) {
            this._submitFlow(navCtrl, writerFlow, userList, writerFlow.selectStepID);
        } else {
            ///
            if (writerFlow.isNextStepUserSingle()) {
                this._submitSingleFlow(navCtrl, writerFlow);
            } else {
                this._submitMutiFlow(navCtrl, writerFlow);
            }
        }
    }

    /**
     * 
     * 
     * @private
     * @param {LightWriterFlow} writerFlow 
     * @memberof LightFlowService
     */
    private _submitSingleFlow(navCtrl: NavController, writerFlow: LightWriterFlow): void {
        var userList = writerFlow.getNextStepUserList();
        if (!!userList && userList.length > 0) {
            this._submitSinglePeopleFlow(navCtrl, writerFlow);
        } else {
            this._submitSingleEmptyFlow(navCtrl, writerFlow);
        }
    }

    /**
     * 
     * 
     * @private
     * @memberof LightFlowService
     */
    private _submitSinglePeopleFlow(navCtrl: NavController, writerFlow: LightWriterFlow): void {
        this.lightProviders.lightModal.push(HandleSinglePeopleComponent, { data: writerFlow.getNextStepUserList() }, value => {
            if (!!value) {
                this._submitFlow(navCtrl, writerFlow, value, writerFlow.getSelectStepID());
            }
        });
    }

    /**
    * 
    * 
    * @private
    * @memberof LightFlowService
    */
    private _submitSingleEmptyFlow(navCtrl: NavController, writerFlow: LightWriterFlow): void {
        this.lightProviders.lightModal.push(HandleSingleContactComponent, { data: writerFlow.getNextStepUserList() }, value => {
            if (!!value) {
                this._submitFlow(navCtrl, writerFlow, value, writerFlow.getSelectStepID());
            }
        });
    }

    /**
     * 
     * 
     * @private
     * @param {LightWriterFlow} writerFlow 
     * @memberof LightFlowService
     */
    private _submitMutiFlow(navCtrl: NavController, writerFlow: LightWriterFlow): void {
        var userList = writerFlow.getNextStepUserList();
        if (!!userList && userList.length > 0) {
            this._submitMutiPeopleFlow(navCtrl, writerFlow);
        } else {
            this._submitMutiEmptyFlow(navCtrl, writerFlow);
        }
    }

    /**
     * 
     * 
     * @private
     * @memberof LightFlowService
     */
    private _submitMutiPeopleFlow(navCtrl: NavController, writerFlow: LightWriterFlow): void {
        this.lightProviders.lightModal.push(HandleMutiPeopleComponent, { data: writerFlow.getNextStepUserList() }, value => {
            if (!!value) {
                this._submitFlow(navCtrl, writerFlow, value, writerFlow.getSelectStepID());
            }
        });
    }

    /**
    * 
    * 
    * @private
    * @memberof LightFlowService
    */
    private _submitMutiEmptyFlow(navCtrl: NavController, writerFlow: LightWriterFlow): void {
        this.lightProviders.lightModal.push(HandleMutiContactComponent, { data: writerFlow.getNextStepUserList() }, value => {
            if (!!value) {
                this._submitFlow(navCtrl, writerFlow, value, writerFlow.getSelectStepID());
            }
        });
    }

    /**
     * 
     * @private
     * @param {LightWriterFlow} writerFlow 
     * @param {*} userList 
     * @param {string} nextStepID 
     * @memberof LightFlowService
     */
    private _submitFlowStep(navCtrl: NavController, writerFlow: LightWriterFlow, userList: any): void {
        this.lightProviders.lightModal.push(HandleStepComponent, { data: writerFlow.getNextStepList() }, value => {
            if (!!value) {
                writerFlow.selectStepID = value;
                this.writerService.getNextWriterUserFlow(writerFlow, value).then(result => {
                    this._submitUserFlow(navCtrl, result);
                }).catch(errText => {
                    this.lightProviders.lightPop.errMessage("");
                })
            }
        });
    }

    /**
     * 
     * @private
     * @param {LightWriterFlow} writerFlow 
     * @param {*} userList 
     * @param {string} nextStepID 
     * @memberof LightFlowService
     */
    private _submitFlow(navCtrl: NavController, writerFlow: LightWriterFlow, userList: any, nextStepID: string): void {
        // 
        let loadctrl = this.lightLoading.showLoading('');
        this.writerService.submitForm(writerFlow, userList, nextStepID).then(value => {
            // 
            loadctrl.instance && loadctrl.dismiss();
            LightFlowService.flowDoneFlag = true;
            this.lightProviders.lightNav.backToRoot(navCtrl);
            this.lightProviders.lightPop.message(value);
        }).catch(errText => {
            loadctrl.instance && loadctrl.dismiss();
            this.lightProviders.lightPop.errMessage("");
        })
    }

    /**
    * 
    * 
    * @memberof LightFlowHandleComponent
    */
    public archiveFlow(navCtrl: NavController, writerFlow: LightWriterFlow, writerData: any): void {
        this.writerService.saveWriterForm(writerFlow, LightJsonUtil.toJson(writerData), true).then(value => {
            this.writerService.archiveFlow(writerFlow).then(value => {
                LightFlowService.flowDoneFlag = true;
                this.lightProviders.lightNav.backToRoot(navCtrl);
                this.lightProviders.lightPop.message("");
            });
        }).catch(err => {
            this.lightProviders.lightPop.errMessage(err);
        })
    }

    /**
     * 
     * @param navCtrl 
     * @param writerFlow 
     */
    public transferFlow(navCtrl: NavController, writerFlow: LightWriterFlow, delivePeople: string, reason: string): void {
        this.lightProviders.lightHttp.get("Flow/TransferFlow", { objectID: writerFlow.objectID, curStepID: writerFlow.getCurStepID(), user: delivePeople, reason: reason }).then(value => {
            LightFlowService.flowDoneFlag = true;
            this.lightProviders.lightNav.backToRoot(navCtrl);
            this.lightProviders.lightPop.message("");
        }).catch(errText => {
            this.lightProviders.lightPop.errMessage(errText);
        });
    }

}


