import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { OrganizeObject } from '../common/OrganizeObject';

@Component({
    //moduleId: module.id,
    selector: 'contact-menu',
    templateUrl: 'contact-menu.component.html',
    //styleUrls: ['contact-menu.component.scss']
})
export class ContactMenuComponent implements OnInit {

    //
    @Input() organizeList: Array<OrganizeObject>;

    //
    @Output() selectEvent = new EventEmitter<OrganizeObject>();

    //
    ngOnInit(): void {

    }

    //
    goSelect(organzieObject: OrganizeObject): void {
        this.selectEvent.emit(organzieObject);
    }

}



