import { Injectable } from '@angular/core';
import { ModalController, ViewController } from 'ionic-angular';

//
@Injectable()
export class LightModal {

    // private _viewController:ViewController=null;

    /**
     * 
     * 
     * @param {ModalController} modal 
     * @memberof LightModal
     */
    constructor(
        public modalCtrl: ModalController
    ) {

    }

    /**
     * 
     * @param {NavController} navCtrl 
     * @memberof LightModal
     */
    public paramInit(navCtrl: ViewController): void {

    }

    /**
     * 
     * @param {*} page 
     * @param {*} data 
     * @memberof LightModal
     */
    public push(page: any, data: any, callback: (...args: any[]) => void = null): void {
        let profileModal = this.modalCtrl.create(page, data);
        profileModal.onDidDismiss(data => {
            if (!!callback) {
                callback(data);
            }
        });
        profileModal.present();
    }

    /**
     * 
     * @param {*} page 
     * @param {*} data 
     * @memberof LightModal
     */
    public push2(page: any, callback: (...args: any[]) => void = null): void {
        let profileModal = this.modalCtrl.create(page);
        profileModal.onDidDismiss(data => {
            if (!!callback) {
                callback(data);
            }
        });
        profileModal.present();
    }

    /**
     * 
     */
    public dismiss(navCtrl: ViewController, data: any = null): void {
        navCtrl.dismiss(data);
    }

}


