import { IFormNode } from "./IFormNode";
import { FormRowBase } from './FormRowBase';
import { FormEvent } from './FormEvent';

/**
 * 
 * @export
 * @interface IFormRow
 */
export class FormNode extends FormRowBase implements IFormNode {

    //key
    private _valueKey: string = "value";

    //
    //value: any="";

    constructor(dataOject: any, formEvent: FormEvent = null) {
        super(dataOject, formEvent);
        // if (!!dataOject) {
        //     this.value = dataOject[this._valueKey];
        // }
    }

    /**
     * value
     * 
     * @readonly
     * @type {*}
     * @memberof FormNode
     */
    get value(): any {
        var result: any = "";
        if (!!this.dataObject) {
            result = this.dataObject[this._valueKey];
        }
        return result;
    }

    /**
     * value
     * 
     * @memberof FormNode
     */
    set value(temp: any) {
        if (!!this.dataObject) {
            this.dataObject[this._valueKey] = temp;
        }
    }

}


