import { Injectable } from '@angular/core';

//
@Injectable()
export class LightLocalCache {

    //
    private _localStorage: any;

    // 
    constructor(

    ) {
        if (!localStorage) {
            throw new Error("localStorage");
        } else {
            console.log(localStorage);
            this._localStorage = localStorage;
        }
    }

    /**
     * Key
     * @param {string} key 
     * @returns {boolean} 
     * 
     * @memberof LightLocalCache
     */
    public contain(key: string): boolean {
        var result: boolean = false;
        if (this._isValidate(key)) {
            for (var i = 0; i < this._localStorage.length; i++) {
                var keyTemp: string = this._localStorage.key(i);
                if (!!keyTemp && keyTemp == key) {
                    result = true;
                    break;
                }
            }
        }
        return result;
    }

    /**
     * 
     * @param {string} Key 
     * @param {string}  
     * 
     * @memberof LightLocalCache
     */
    public set(key: string, value: string): void {
        if (this._isValidate(key)) {
            this._localStorage[key] = value;
        }
    }

    /**
     * 
     * @param {string} key
     * @param {object}  
     * @memberof LightLocalCache
     */
    public setObject(key: string, value: object): void {
        if (this._isValidate(key)) {
            if (!!value) {
                this._localStorage[key] = JSON.stringify(value);
            }
        }
    }

    //
    public get(key: string): string {
        var result: string = "";
        if (this._isValidate(key)) {
            result = this._localStorage[key];
        }
        return result;
    }

    /**
     * 
     * @param {string} key 
     * @returns {object} 
     * 
     * @memberof LightLocalCache
     */
    public getObject(key: string): object {
        var result: object = null;
        if (this._isValidate(key)) {
            result = JSON.parse(this._localStorage[key] || '{}');
        }
        return result;
    }

    /**
     * Key
     * 
     * @param {string} key 
     * 
     * @memberof LightLocalCache
     */
    public remove(key: string): void {
        if (this._isValidate(key)) {
            this._localStorage.removeItem(key);
        }
    }

    /**
     * 
     * @memberof LightLocalCache
     */
    public clear(): void {
        if (!!this._localStorage) {
            this._localStorage.clear();
        }
    }

    //
    private _isValidate(key: string): boolean {
        var result: boolean = false;
        if (!!this._localStorage && !!key) {
            result = true;
        }
        return result;
    }

}


