import { Component, Input, OnChanges } from "@angular/core";
import { NavController, NavParams, AlertController } from "ionic-angular";
import {
  LightHttp,
  LightPop
} from "../../light-providers/light-providers.module";
import { LightStringUtil } from "../../light-util/light-util.module";

@Component({
  selector: "light-attach",
  templateUrl: "light-attach.component.html"
})
export class LightAttachComponent implements OnChanges {
  /**
   *
   *
   * @type {Array<any>}
   * @memberof LightAttachComponent
   */
  @Input() attachList: Array<{
    Code: string;
    FilesLength: string;
    filesLength: string;
    Name: string;
    fileName: string;
    Path: string;
    fileUrl: string;
    Status: string;
    current: number;
    downloading: boolean;
    timer: any;
  }>;

  // 
  max: number = 1;

  /**
   * 
   * @memberof LightAttachComponent
   */
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public alertCtrl: AlertController,
    public lightHttp: LightHttp,
    public lightPop: LightPop,
  ) {}

  /**
   *
   * 
   */
  ngOnChanges(): void {
    if (!!this.attachList) {
      // 
      this.attachList.forEach((attach, index) => {
        this.attachList[index].Status = "";
      });
    }
  }

  /**
   * 
   *
   * @param {any} item
   * @param {any} index
   * @memberof LightAttachComponent
   */
  downloadFile(item: any, index: any) {
    console.log("")
  }

  /**
   * 
   *
   * @param {any} input
   * @returns {*}
   * @memberof NewDetailPage
   */
  public fileTypeImage(input: any): any {
    var out = "";
    if (
      LightStringUtil.contain(input, "jpg") ||
      LightStringUtil.contain(input, "png") ||
      LightStringUtil.contain(input, "gif")
    ) {
      out = "jpg";
    } else if (
      LightStringUtil.contain(input, "ppt") ||
      LightStringUtil.contain(input, "pptx")
    ) {
      out = "ppt";
    } else if (LightStringUtil.contain(input, "txt")) {
      out = "txt";
    } else if (
      LightStringUtil.contain(input, "doc") ||
      LightStringUtil.contain(input, "docx") ||
      LightStringUtil.contain(input, "wps")
    ) {
      out = "word";
    } else if (
      LightStringUtil.contain(input, "xls") ||
      LightStringUtil.contain(input, "xlsx")
    ) {
      out = "xls";
    } else if (
      LightStringUtil.contain(input, "zip") ||
      LightStringUtil.contain(input, "7z")
    ) {
      out = "zip";
    } else if (LightStringUtil.contain(input, "pdf")) {
      out = "pdf";
    } else {
      out = "unknown";
    }
    out = "assets/img/attach/" + out + "_128.png";
    return out;
  }

  /**
   *
   *
   * @param {*} fileByte
   * @returns
   * @memberof LightAttachComponent
   */
  public getFileSize(fileByte: any) {
    var fileSizeByte = fileByte;
    var fileSizeMsg = "";
    if (fileSizeByte < 1048576)
      fileSizeMsg = (fileSizeByte / 1024).toFixed(2) + "KB";
    else if (fileSizeByte == 1048576) fileSizeMsg = "1MB";
    else if (fileSizeByte > 1048576 && fileSizeByte < 1073741824)
      fileSizeMsg = (fileSizeByte / (1024 * 1024)).toFixed(2) + "MB";
    else if (fileSizeByte > 1048576 && fileSizeByte == 1073741824)
      fileSizeMsg = "1GB";
    else if (fileSizeByte > 1073741824 && fileSizeByte < 1099511627776)
      fileSizeMsg = (fileSizeByte / (1024 * 1024 * 1024)).toFixed(2) + "GB";
    else fileSizeMsg = "1TB";
    return fileSizeMsg;
  }
}



