import { IFormRow } from "./IFormRow";
import { IFormAction } from "./IFormActive";
import { FormRowBaseUtil } from './FormRowBaseUtil';
import { FormEvent } from './FormEvent';
import { FormListAction } from './FormListAction';
import { FormNodeAction } from './FormNodeAction';

/**
 * 
 * @export
 * @interface IFormRow
 */
export class FormRowBase implements IFormRow {

    ///Key
    protected _titleKey: string = "txt";

    ///Key
    protected _typeKey: string = "type";

    ///Key
    protected _detailTypeKey: string = "detailtype";

    ///Key
    protected _etendObjectKey: string = "extend";

    ///Key
    protected _actionObjectKey: string = "action";

    ///Key
    protected _actionObjectTypeKey: string = "type";

    //
    public dataObject: any = null;

    ///
    title: string;

    ///
    type: string;

    ///
    detailType: string;

    ///
    extendObject: object;

    ///
    isNode: boolean;

    ///
    activeData: IFormAction;

    ///
    formEvent: FormEvent;

    ///
    constructor(dataOject: any, formEvent: FormEvent = null) {
        this.dataObject = dataOject;
        this.formEvent = formEvent;
        if (!!this.dataObject) {
            this.type = this.dataObject[this._typeKey];
            this.extendObject = this.dataObject[this._etendObjectKey];
            this.title = this.dataObject[this._titleKey];
            this.detailType = this.dataObject[this._detailTypeKey];
            this.isNode = FormRowBaseUtil.isNode(this.type);
            var actionObject = this.dataObject[this._actionObjectKey];
            if (!!actionObject) {
                var actionType = actionObject[this._actionObjectTypeKey];
                if (!!actionType) {
                    if (actionType == "list") {
                        this.activeData = new FormListAction(actionObject);
                    } else if (actionType == "node") {
                        this.activeData = new FormNodeAction(actionObject);
                    } else {

                    }
                }
            }
        }
    }

}


