import { Component, Input, OnInit } from '@angular/core';
import { IFormList, FormListAction } from '../../light-common/index';

@Component({
  selector: 'light-read-group',
  templateUrl: 'light-read-group.component.html',
  // styleUrls: ['light-read-group.component.scss']
})
//
export class LightReadGroupComponent implements OnInit {

  //
  @Input() rowList: IFormList;

  //
  title: string;

  //
  showFlag: boolean = true;

  //
  showStatus: string = "";

  //
  headFlag: boolean = true;

  //
  ngOnInit(): void {
    this.title = this.rowList.title;
    var actionList = this.rowList.activeData as FormListAction;
    if (!!actionList) {
      this.headFlag = actionList.headVisable;
      this.showFlag = actionList.displayVisable;
    }
    this._showStatusInit(this.showFlag);
  }

  //
  showClick(): void {
    if (this.showFlag) {
      this.showFlag = false;
    } else {
      this.showFlag = true;
    }
    this._showStatusInit(this.showFlag);
  }

  ///
  private _showStatusInit(flag: boolean): void {
    if (!flag) {
      this.showStatus = "";
    } else {
      this.showStatus = "";
    }
  }

}



