import { Component, Input, OnInit, SimpleChanges } from '@angular/core';
import { FormRowUtil, IFormList, FormActionMap, FormEvent } from '../light-common/index';

@Component({
    selector: 'light-read',
    templateUrl: 'light-read.component.html'
    // styleUrls: ['light-read.component.scss']
})
export class LightReadComponent implements OnInit {

    //
    @Input() readData: any = {};

    //
    formListArray: Array<IFormList> = [];

    //
    formActionMap: FormActionMap = null;

    //
    formEvent: FormEvent = null;

    //
    constructor(

    ) {

    }

    /**
     * 
     * @memberof LightReadComponent
     */
    public ngOnInit(): void {
        this.formListArray = [];
        this.formActionMap = new FormActionMap();
        this.formEvent = new FormEvent();
        this._flush();
    }

    ngOnChanges(changes: SimpleChanges): void {
        this.ngOnInit();
    }


    /**
     * 
     * @private
     * @memberof LightReadComponent
     */
    private _flush(): void {
        if (!!this.readData) {
            this.formListArray = FormRowUtil.getFormDta(this.readData, this.formEvent);
        }
    }

}



