import { SearchPage } from './../pages/search/search';
import { WechatPage } from './../pages/wechat/wechat';
import { NgModule, ErrorHandler } from "@angular/core";
import { BrowserModule } from "@angular/platform-browser";
import { IonicApp, IonicModule, IonicErrorHandler } from "ionic-angular";
import { MyApp } from "./app.component";

//
import { ModalFromBottomLeave, ModalFromBottomEnter } from "./modal-transition";
import { Config } from "ionic-angular";

//
import { LoginPage, TabsPage } from "../pages/common/index";
import { HomePage } from "../pages/home/home";
import { WorkPageComponent } from "../pages/work/work-page.component";


//
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

///ajax
import { CommonService } from "./CommonService";

//lightout
import {
  LightOutModule,
  LightListComponent
} from "../light/light-out/light-out.module";

@NgModule({
  declarations: [
    MyApp,
    LoginPage,
    HomePage,
    WorkPageComponent,
    TabsPage,
    WechatPage,
    SearchPage
  ],
  imports: [
    BrowserModule,
    LightOutModule,
    IonicModule.forRoot(MyApp, {
      backButtonIcon: "ios-arrow-back-outline",//
      backButtonText: "",//
      tabsHideOnSubPages: "true",
      iconMode: "md",
      mode: "md"
    })
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    LoginPage,
    HomePage,
    WorkPageComponent,
    TabsPage,
    WechatPage,
    SearchPage
  ],
  exports: [LightListComponent],
  providers: [
    StatusBar,
    SplashScreen,
    CommonService,
    { provide: ErrorHandler, useClass: IonicErrorHandler }
  ]
})
export class AppModule {

  constructor(public config: Config) {
    this.setCustomTransitions();
  }

  private setCustomTransitions() {
    this.config.setTransition("modal-from-bottom-enter", ModalFromBottomEnter);
    this.config.setTransition("modal-from-bottom-leave", ModalFromBottomLeave);
  }

}



