/**
 * 
 * 
 * @export
 * @class LightObjectUtil
 */
export class LightObjectUtil {

    /**
     * 
     * 
     * @static
     * @param {*} 
     * @param {string}  
     * @param {string}  
     * @returns {string} 
     * 
     * @memberof LightObjectUtil
     */
    public static GetValue(target: any, propereName: string, defaultValue?: any): any {
        var result: any = defaultValue;
        if (!!target && !!propereName && !!target[propereName]) {
            result = target[propereName];
        }
        return result;
    }

    /**
     * 
     * @static
     * @param {*} target 
     * @returns {string} 
     * @memberof LightObjectUtil
     */
    public static GetObjectIndexString(target: any): string {
        var result: string = "";
        if (!!target) {
            var listArray: Array<string> = [];
            for (var key in target) {
                if (target.hasOwnProperty(key)) {
                    var element = target[key];
                    listArray.push(element);
                }
            }
            result = listArray.join("_");
        }
        return result;
    }

}


