import { Component } from '@angular/core';
import { ViewController } from 'ionic-angular';
import { UserObject } from '../../light-contact/index';
import { LightArrayUtil } from '../../../light-util/light-util.module';
import { LightProviders } from '../../../light-providers/light-providers.module';

@Component({
  selector: 'handle-muti-contact',
  templateUrl: 'handle-muti-contact.component.html'
})
export class HandleMutiContactComponent {

  //
  userList: Array<any> = [];

  constructor(
    public lightProviders: LightProviders,
    public viewCtrl: ViewController
  ) {

  }

  /**
   * 
   * @param {UserObject} userObject 
   * @memberof HandleMutiContactComponent
   */
  contactSelect(userObject: UserObject): void {
    if (!LightArrayUtil.contain(this.userList, userObject.Name)) {
      this.userList.push(userObject.Name);
    }
  }

  /**
   * 
   * @memberof HandleMutiContactComponent
   */
  goBack(): void {
    this.lightProviders.lightModal.dismiss(this.viewCtrl);
  }

  /**
   * 
   * @memberof HandleMutiContactComponent
   */
  submit(): void {
    if (this.userList.length == 0) {
      this.lightProviders.lightPop.errMessage("");
    } else {
      this.lightProviders.lightModal.dismiss(this.viewCtrl, this.userList);
    }
  }

  /**
   * 
   * 
   * @param {string} userName 
   * @memberof HandleMutiContactComponent
   */
  remove(index: number): void {
    this.userList.splice(index, 1);
  }

}



