/*
 * 
 *
 * @export
 * @class LightUtil
 */
export class LightArrayUtil {

    /**
     * 
     * @static
     * @param {Array<any>} 
     * @returns {boolean}
     * @memberof LightArrayUtil
     */
    public static isEmpty(array: Array<any>): boolean {
        var result: boolean = false;
        if (!!!array || array.length == 0) {
            result = true;
        }
        return result;
    }

    /**
    * 
    * @static
    * @param {Array<any>} array
    * @param {*} value
    * @returns {number}
    *
    * @memberof LightArrayUtil
    */
    public static getIndexOf(array: Array<any>, value: any): number {
        var result: number = -1;
        if (!LightArrayUtil.isEmpty(array)) {
            for (var i: number = 0; i < array.length; i++) {
                if (array[i] == value) {
                    result = i;
                    break;
                }
            }
        }
        return result;
    }

    /**
     * 
     *
     * @static
     * @param {Array<any>} array
     * @param {*} value
     * @returns {number}
     *
     * @memberof LightArrayUtil
     */
    public static getLastIndexOf(array: Array<any>, value: any): number {
        var result: number = -1;
        if (!!array && array.length >= 0) {
            for (var i: number = array.length - 1; i >= 0; i--) {
                if (array[i] == value) {
                    result = i;
                    break;
                }
            }
        }
        return result;
    }

    /**
     *
     * 
     * @static
     * @param {Array<any>} 
     * @param {*} 
     * @returns {boolean}
     *
     * @memberof LightArrayUtil
     */
    public static contain(array: Array<any>, value: any): boolean {
        var result: boolean = false;
        if (!LightArrayUtil.isEmpty(array)) {
            var index: number = LightArrayUtil.getIndexOf(array, value);
            if (index >= 0) {
                result = true;
            }
        }
        return result;
    }


    /**
     *var dataArray:Array<any>= [{Name:"a"},{Name:"b"},{Name:"c"}]
     * var index=LightArrayUtil.getFieldIndexOf(dataArray,"Name","c");
     * 
     *
     * @static
     * @param {Array<any>} array
     * @param {string} fieldName
     * @param {*} value
     * @returns {number}
     *
     * @memberof LightArrayUtil
     */
    public static getFieldIndexOf(array: Array<any>, fieldName: string, value: any): number {
        var result: number = -1;
        if (!LightArrayUtil.isEmpty(array) && !!fieldName) {
            for (var i: number = 0; i < array.length; i++) {
                var tempObject: any = array[i];
                if (!!tempObject && tempObject[fieldName] == value) {
                    result = i;
                    break;
                }
            }
        }
        return result;
    }

    /**
     * 
     * @static
     * @param {Array<any>} 
     * @param {string} 
     * @param {*} 
     * @returns {boolean}
     *
     * @memberof LightArrayUtil
     */
    public static containField(array: Array<any>, fieldName: string, value: any): boolean {
        var result = false;
        if (!LightArrayUtil.isEmpty(array) && !!fieldName) {
            var index: number = LightArrayUtil.getFieldIndexOf(array, fieldName, value);
            if (index >= 0) {
                result = true;
            }
        }
        return result;
    }


    /**
     * 
     *
     * @static
     * @param {Array<any>} 
     *
     * @memberof LightArrayUtil
     */
    public static clear(array: Array<any>): void {
        if (!!array) {
            array.splice(0, array.length);
        }
    }

    /**
     * 
     *
     * @static
     * @param {Array<any>} 
     * @param {number} 
     * @returns {boolean}
     *
     * @memberof LightArrayUtil
     */
    public static removeIndex(array: Array<any>, index: number): boolean {
        var result: boolean = false;
        if (!!array && index >= 0) {
            array.splice(index, 1);
            result = true;
        }
        return result;
    }

    /**
     * 
     *
     * @static
     * @param {Array<any>} 
     * @param {*} 
     * @returns {boolean}
     *
     * @memberof LightArrayUtil
     */
    public static remove(array: Array<any>, value: any): boolean {
        var result: boolean = false;
        var index: number = LightArrayUtil.getIndexOf(array, value);
        if (index >= 0) {
            array.splice(index, 1);
            result = true;
        }
        return result;
    }


    /**
     *
     * Array
     * @static
     * @param {Array<any>} source
     * @param {Array<any>} target
     *
     * @memberof LightArrayUtil
     */
    public static joinArray(source: Array<any>, target: Array<any>): void {
        if (!!source && !!target && target.length > 0) {
            target.forEach(element => {
                source.push(element);
            });
        }
    }

    /**
     * 
     * @static
     * @param {...Array<any>} args
     * @returns {Array<any>}
     *
     * @memberof LightArrayUtil
     */
    public static joinArrayList(...args: Array<Array<any>>): Array<any> {
        var result: Array<any> = [];
        if (!!args && args.length > 0) {
            for (var index = 0; index < args.length; index++) {
                var element = args[index] as Array<any>;
                element.forEach(item => {
                    result.push(item);
                });
            }
        }
        return result;
    }

}



