import { Component, Input, OnInit } from '@angular/core';
import { IFormRow } from '../../light-common/index';

@Component({
    //moduleId: module.id,
    selector: 'light-read-row',
    templateUrl: 'light-read-row.component.html',
    //styleUrls: ['light-read-row.component.scss']
})
export class LightReadRowComponent implements OnInit {

    //
    @Input() rowList: IFormRow[];

    showFlag: boolean = true;

    title: string = null;

    showEvent(): void {
        if (this.showFlag) {
            this.showFlag = false;
        } else {
            this.showFlag = true;
        }
    }

    public ngOnInit(): void {
        if (!!this.rowList && this.rowList.length > 1) {
            this.title = this.rowList[0].title;
        }
    }

}



