import { IFormAction } from './IFormActive';
import { FormActionBase } from './FormActionBase';

/**
 * 
 * @export
 * @interface IFormAction
 */
export class FormNodeAction extends FormActionBase implements IFormAction {

    ///Key
    private _actionListKey: string = "list";

    ///Key
    private _actionStatusKey: string = "status";

    ///key
    private _actionDetailKey: string = "detail";

    ///
    actionList: string;

    ///
    actionStatus: string;

    ///
    actionDetail: string;

    ///
    constructor(
        dataObject: any
    ) {
        super(dataObject);
        if (!!dataObject) {
            this.actionList = dataObject[this._actionListKey];
            this.actionStatus = dataObject[this._actionStatusKey];
            this.actionDetail = dataObject[this._actionDetailKey];
        }
    }

}


