import { Input } from '@angular/core';
import { IFormNode } from '../../light-common/index';
/**
 * 
 *
 * @export
 * @class LightWriterComponent
 */
export class LightWriterCommon {

    //
    @Input() formRow: IFormNode = null;

    //
    key: string = "";

    //
    value: any;

    //
    rowType: string = "";

    //
    detailType: string = "";

    /**
     * 
     *
     * @memberof LightWriterCommon
     */
    public ngOnInit(): void {
        this.key = this.formRow.title;
        this.value = this.formRow.value;
        this.rowType = this.getRowType();
        this.detailType = this.getDetailType();
    }

    /**
     * 
     */
    public getRowType(): string {
        var result: string = null;
        if (!!this.formRow) {
            result = this.formRow.type;
        }
        return result;
    }

    /**
     * 
     *
     * @returns {string}
     * @memberof LightWriterComponent
     */
    public getDetailType(): string {
        var result: string = null;
        if (!!this.formRow) {
            result = this.formRow.detailType;
        }
        return result;
    }

    /**
     * 
     * @returns {*}
     * @memberof LightWriterComponent
     */
    public getExtendObject(): any {
        var result: any = null;
        if (!!this.formRow) {
            result = this.formRow.extendObject;
        }
        return result;
    }

    /**
     *
     * 
     * @param {string} propertyName
     * @returns {*}
     * @memberof LightWriterComponent
     */
    public getExtendObjectValue(propertyName: string): any {
        var result: any = null;
        var extendObject = this.getExtendObject();
        if (!!extendObject) {
            result = extendObject[propertyName];
        }
        return result;
    }

}



