import { IFormAction } from './IFormActive';

/**
 * 
 * @export
 * @interface IFormAction
 */
export class FormActionBase implements IFormAction {

    ///Key
    private _typeKey: string = "type";

    ///
    private _visiableType: string = "visiable";

    ///
    actionData: any;

    ///ID
    activeID: string;

    ///
    visiable: boolean;

    ///
    type: string;

    /**
     * Creates an instance of FormActionBase.
     * @param {*} dataObject 
     * @memberof FormActionBase
     */
    constructor(dataObject: any) {
        this.actionData = dataObject;
        if (!!this.actionData) {
            this.type = this.actionData[this._typeKey];
            this.visiable = this.actionData[this._visiableType];
        }
    }

}


