import { Component, OnInit } from '@angular/core';
import { LightWriterCommon } from '../common/index';

@Component({
    selector: 'light-writer-date',
    templateUrl: 'light-writer-date.component.html',
    // styleUrls: ['light-writer-date.component.scss']
})
export class LightWriterDateComponent extends LightWriterCommon implements OnInit {

    // 
    dateArr: any;

    /**
     * 
     * @memberof LightWriterDateComponent
     */
    constructor(

    ) {
        super();
    }

    /**
     * 
     */
    public ngOnInit() {
        super.ngOnInit();
        if ('dateArray' in this.getExtendObject()) {
            this.dateArr = this.getExtendObject().dateArray
        } else {
            this.formRow['txt'] = this.formRow.title;
            this.formRow['extend'] = this.getExtendObject();
            this.dateArr = [this.formRow]
        };
    }

}



