import { IFormRow } from './IFormRow';
import { IFormNode } from './IFormNode';
import { IFormList } from './IFormList';
import { FormEvent } from './FormEvent';
import { FormRowBase } from './FormRowBase';

/**
 * 
 * @export
 * @interface IFormRow
 */
export class FormList extends FormRowBase implements IFormList {

    //
    startRow: IFormNode;

    //
    childData: Array<IFormRow>;

    //
    endRow: IFormNode;

    //
    formEvent: FormEvent;

    //
    constructor(
        dataOject: any,
        formEvent: FormEvent = null
    ) {
        super(dataOject, formEvent);
        this.childData = [];
    }

    /**
     * 
     * 
     * @param {IFormRow} formRow 
     * @memberof FormList
     */
    addRowData(formRow: IFormRow): void {
        this.childData.push(formRow);
    }


    /**
     * 
     * 
     * @returns {Array<IFormRow>} 
     * @memberof FormList
     */
    getRowData(): Array<IFormRow> {
        var result: Array<IFormRow> = [];
        return result;
    }

}


