import { Component, OnInit } from '@angular/core';
import { LightWriterFlow } from '../../light-writer/index';
import { LightJsonUtil } from '../../../light-util/light-util.module';
import { NavController, NavParams } from 'ionic-angular';
import { LightFlowService } from '../light-flow.service';

@Component({
    selector: 'light-flow-handle',
    templateUrl: 'light-flow-handle.component.html'
})
export class LightFlowHandleComponent implements OnInit {

    ///
    lightFlow: LightWriterFlow = null;

    ///
    writerData: any;

    //
    isArchiveFlow: boolean = false;

    /**
     * Creates an instance of LightFlowHandleComponent.
     * @param {LightWriterService} lightWriterService 
     * @param {NavParams} navParam 
     * @memberof LightFlowHandleComponent
     */
    constructor(public navCtrl: NavController, public navParam: NavParams, public flowService: LightFlowService) {
        this.lightFlow = this.navParam.get("flow") as LightWriterFlow;
        console.log(this.lightFlow);
        this._Init();
    }

    /**
     * 
     */
    private _Init(): void {
        this.isArchiveFlow = this.lightFlow.isArchiveFlow();
    }

    /**
      * 
      * @memberof LightContentComponent
      */
    ngOnInit(): void {
        this.flowService.getWriterForm(this.lightFlow).then(value => {
            this.writerData = LightJsonUtil.toObject(value);
        });
    }

    /**
     * 
     * 
     * @memberof LightFlowHandleComponent
     */
    public submitTaskDetail(): void {
        this.flowService.submitFlow(this.navCtrl, this.lightFlow, this.writerData);
    }

    /**
     * 
     * 
     * @memberof LightFlowHandleComponent
     */
    public archiveFlow(): void {
        this.flowService.archiveFlow(this.navCtrl, this.lightFlow, this.writerData);
    }

}



