import { Component, OnInit } from '@angular/core';
import { LightWriterCommon } from '../common/index';

@Component({
    //moduleId: module.id,
    selector: 'light-writer-textarea',
    templateUrl: 'light-writer-textarea.component.html',
    //styleUrls: ['light-writer-textarea.component.scss']
})
///
export class LightWriterTextareaComponent extends LightWriterCommon implements OnInit {

    /**
     * 
     * @memberof LightWriterTextareaComponent
     */
    constructor() {
        super();
    }

    ngOnInit(): void {
        super.ngOnInit();
        //this.key = this.formRow.title;
        //this.value=this.rowInfo.value+"";
    }

    ///
    onblur(event: any): void {
        var changeText: string = event.target.innerText;
        this.formRow.value = changeText;
    }

}



