import { Component, OnInit, AfterContentChecked } from '@angular/core';
import { NavController, IonicPage, App } from 'ionic-angular';
import { LightStringUtil, LightFlowService } from '../../light/light-out/light-out.module';
import { TabsServices } from '../common/tabs/tabs.service';

@Component({
  //moduleId: module.id,
  selector: 'work-page',
  templateUrl: 'work-page.component.html',
  //styleUrls: ['work-page.component.scss']
})
export class WorkPageComponent implements OnInit, AfterContentChecked {

  ///
  doingDataUrl: string = "Flow/GetDoingFlowList";

  ///
  createDataUrl: string = "Flow/GetCreatedByMeDoneFlowList";

  ///
  todoDataUrl: string = "Flow/GetToDoByUserName";

  ///
  byOtherDoneDataUrl: string = "Flow/GetCreatedByOtherDoneFlowList";

  ///
  dataurl: string = "";

  ///
  datalist: Array<any> = [];

  ///
  dataParam: any = {};

  ///
  pet: string = "todoProcess";

  //
  isFirstComing: boolean = true;

  /**
   * 
   * @param {NavController} navCtrl
   * @param {NavParams} navParams
   * @param {LightProviders} lightProviders
   * @memberof WorkPageComponent
   */
  constructor(
    public navCtrl: NavController,
    private app: App
  ) {

  }

  /**
   * 
   * @memberof WorkPageComponent
   */
  public ngOnInit(): void {
    let params = TabsServices.getTabParams();
    let switchIndex = params ? params : this.pet;
    this.pet = switchIndex;
    this.flush();
  }

  ionViewWillEnter() {
    if (!this.isFirstComing) {
      this.datalist = []
      this.ngOnInit();
    }
    this.isFirstComing = false;
  }

  ///
  public ngAfterContentChecked(): void {
    if (LightFlowService.flowDoneFlag) {
      LightFlowService.flowDoneFlag = false;
      this.flush(true);
    }
  }


  /**
   * 
   *
   * @private
   * @memberof WorkPageComponent
   */
  public flush(flag: boolean = false): void {
    if (flag) {
      this.dataurl = "";
    }
    if (this.pet == "todoProcess") {
      this.dataurl = this.todoDataUrl;
    }
    else if (this.pet == "processing") {
      this.dataurl = this.doingDataUrl;
    }
    else if (this.pet == "createProcess") {
      this.dataurl = this.createDataUrl;
    }
    else if (this.pet == "handleProcess") {
      this.dataurl = this.byOtherDoneDataUrl;
    }
    else {
      this.dataurl = "";
    }

  }

  //
  processList() {
    this.navCtrl.push("WorkNewProcessListPage");
  }

  //
  workDetail(item, flag) {
    item.showFlag = flag;
    this.navCtrl.push('WorkDetailPage', { item: item });
  }

  /**
   * title
   * @param row
   */
  public titleCheck(row: any): string {
    var result = row.Title;
    if (!!!result) {
      result = row.JobType;
    }
    return result;
  }

  /**
   * 
   *
   * @param {any} input
   * @returns {*}
   * @memberof NewDetailPage
   */
  public flowImage(input: any): any {
    var out = "";
    if (LightStringUtil.contain(input, "")) {
      out = "icon1";
    } else if (LightStringUtil.contain(input, "")) {
      out = "icon3";
    } else if (LightStringUtil.contain(input, "")) {
      out = "icon20";
    } else if (LightStringUtil.contain(input, "")) {
      out = "icon5";
    } else if (LightStringUtil.contain(input, "")) {
      out = "icon15";
    } else if (LightStringUtil.contain(input, "")) {
      out = "icon23";
    } else if (LightStringUtil.contain(input, "")) {
      out = "icon24";
    } else {
      out = "icon1";
    }
    out = "assets/img/icon-work/" + out + ".png";
    return out;
  }

}



