import { OrganizeObject } from './OrganizeObject';
import { UserObject } from './UserObject';
import { LightJsonUtil } from '../../../light-util/light-util.module';
import { LightHttp } from '../../../light-providers/light-providers.module';

///
export class LightContactUtil {

    /**
     * 
     * @type {OrganizeObject}
     * @memberof LightContactService
     */
    private _rootOrganizeObject: OrganizeObject = null;

    private lightHttp: LightHttp = null;

    constructor(
        lightHttp: LightHttp
    ) {
        this.lightHttp = lightHttp;
    }

    /**
     * 
     * @param {string}  
     * @returns {OrganizeObject} 
     * 
     * @memberof LightContactService
     */
    getRootOrganizeObject(url: string, opts?: object): Promise<OrganizeObject> {
        var result: Promise<any> = null;
        if (!!url) {
            var that = this;
            result = new Promise(function (resolve, reject) {
                if (!!that._rootOrganizeObject) {
                    resolve(that._rootOrganizeObject);
                } else {
                    that.lightHttp.post(url, opts).then(
                        value => {
                            value = LightJsonUtil.isJsonText(value) ? LightJsonUtil.toObject(value) : value;
                            that._rootOrganizeObject = that._getDataList(value);
                            resolve(that._rootOrganizeObject);
                        }
                    ).catch(errText => {
                        reject(errText);
                    });
                }
            });
        }
        return result;
    }

    /**
     * 
     * 
     * @private
     * @param {Array<object>}  
     * @returns {OrganizeObject} 
     * 
     * @memberof LightContactService
     */
    private _getDataList(formList: Array<object>): OrganizeObject {
        var result: OrganizeObject = new OrganizeObject();
        if (!!formList && formList.length > 0) {
            formList.forEach(element => {
                if (!!element) {
                    var type: string = element["Type"] as string;
                    var id: string = element["ID"] as string;
                    var pid: string = element["PID"] as string;
                    var temp: OrganizeObject;
                    if (!!type) {
                        if (type == "organize") {
                            temp = result.getOrganizeObject(id);
                            if (!!!temp) {
                                result.childOrganize.push(new OrganizeObject(element, result));
                            }
                        }
                        else if (type == "user") {
                            temp = result.getOrganizeObject(pid);
                            if (!!!temp) {
                                result.childUser.push(new UserObject(element, result.Name, pid));
                            } else {
                                temp.childUser.push(new UserObject(element, temp.Name, pid));
                            }
                        } else {

                        }
                    }

                }
            });
        }
        return result;
    }

    /**
     * 
     * 
     * @memberof LightContactService
     */
    clear(): void {
        this._rootOrganizeObject = null;
    }

}


