import { Injectable } from "@angular/core";
import { Platform } from "ionic-angular";
import { LightNation } from "../common/lightNation";
import { AppMinimize } from "@ionic-native/app-minimize";

/**
 * npm:npm install --save @ionic-native/app-minimize
 * ionic:ionic cordova plugin add cordova-plugin-appminimize
 * :Android
 * 
 * @export
 * @class LightPermissisons
 * @extends {LightNation}
 */
@Injectable()
export class LightMinimize extends LightNation {
  constructor(private platform: Platform, private appMinimize: AppMinimize) {
    super();
  }

  /**
   * app
   */
  minimize(): void {
    if (this.platform.is("cordova")) {
      this.appMinimize.minimize();
    }
  }
}



