/**
 * Json
 *
 * @export
 * @class LightJsonUtil
 */
export class LightJsonUtil {

    /**
     *
     * 
     * @static
     * @param {string} jsonText
     * @returns {boolean}
     *
     * @memberof LightJsonUtil
     */
    public static isJsonText(jsonText: string): boolean {
        var result: boolean = true;
        if (!!jsonText) {
            try {
                var obj = JSON.parse(jsonText);
                if (typeof obj == 'object' && obj) {
                    return true;
                } else {
                    return false;
                }
            } catch (e) {
                console.log('error' + jsonText + '!!!' + e);
                return false;
            }
        } else {
            result = false;
        }
        return result;
    }

    /**
     * Json
     * @static
     * @param {object} 
     * @returns {string}  
     *
     * @memberof LightJsonUtil
     */
    public static toJson(objectTarget: any): string {
        return JSON.stringify(objectTarget);
    }

    /**
     * 
     * @static
     * @param {string} objectText
     * @returns {object}
     *
     * @memberof LightJsonUtil
     */
    public static toObject(objectText: string): object {
        var result: object = null;
        if (!!objectText) {
            result = JSON.parse(objectText);
        }
        return result;
    }
}



