import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { LightProviders, LightApp } from '../../../light/light-out/light-out.module';
import { TabsPage } from '../tabs/tabs';

@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
///
export class LoginPage{
  // export class LoginPage extends LightApp1{

  ///
  userName: string = "";

  ///
  userpassword: string = "";

  /**
   * 
   * @param {NavController} navCtrl
   * @param {NavParams} navParams
   * @param {LightProviders} lightProvider
   * @memberof LoginPage
   */
  constructor(
    public navParams: NavParams,
    public lightProvider: LightProviders,
    public lightApp: LightApp,
    public navCtrl: NavController,
    ) {
    // super(navCtrl, lightProvider)
  }

  /**
   *
   * 
   * @memberof LoginPage
   */
  login() {
    this.lightApp.loginToPage(this.navCtrl, this.userName, this.userpassword, TabsPage);
  }

}



