import { Component, Input, OnInit } from '@angular/core';
import { IFormNode } from '../../light-common/index';

@Component({
    selector: 'light-read-image',
    templateUrl: 'light-read-image.component.html',
    // styleUrls: ['light-read-image.component.scss']
})
export class LightReadImageComponent implements OnInit {

    //
    @Input() formRow: IFormNode = null;

    //
    title: string = "";

    //
    imageUrl: String = "";

    /**
     * 
     * 
     * @memberof
     */
    public ngOnInit(): void {
        this.title = this.formRow.title;
        this.imageUrl = this.formRow.value;
    }
}



