import { Component, Input, OnInit } from '@angular/core';
import { IFormList, IFormRow } from '../../light-common/index';

@Component({
    //moduleId: module.id,
    selector: 'light-read-table',
    templateUrl: 'light-read-table.component.html',
    //styleUrls: ['light-read-table.component.scss']
})
export class LightReadTableComponent implements OnInit {

    //
    @Input() rowList: IFormList;

    /**
     * 
     * @type {Array<any>}
     * @memberof LightWriterTableComponent
     */
    tableRowList: Array<any> = null;

    /**
     * 
     * 
     * @type {String}
     * @memberof LightWriterTableComponent
     */
    title: String = null;

    constructor(

    ) {

    }

    public ngOnInit() {
        this.title = this.rowList.title;
        this.tableRowList = this._getRowList();
    }

    /**
    * 
    * 
    * @private
    * @returns {Array<any>} 
    * @memberof LightWriterTableComponent
    */
    private _getRowList(): Array<any> {
        var result: Array<any> = [];
        var rowInfoList: IFormRow[] = this.rowList.childData;
        var groupList: IFormRow[] = null;
        for (var i = 0; i < rowInfoList.length; i++) {
            var rowInfo: IFormRow = rowInfoList[i];
            if (rowInfo.type == "rowbegin") {
                groupList = [];
                result.push(groupList);
            }
            groupList.push(rowInfo);
        }
        return result;
    }

}



